import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({ providedIn: 'root' })
export class CompanyInformationService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {}

    public bmlfDeleteCompanyInfo(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteCompanyInfo`; //  delete conform ...
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetComDataForDelete(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetComDataForDelete`; // check for delete count
        return this.httpExt.post<any>(url, headerData, null);
    }


    public bmlfSaveCompanyInfo(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveCompanyInfo`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveCompanyInfo`;
            return this.httpExt.post<any>(url, headerData, null);
        }
     }

    public bmlfGetCompanyInfo(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetCompanyInfo`;
        return this.httpExt.post<any>(url, headerData, null);
    }




    public bmlfGetCompanServerPath(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/Login/BMGFGetServerPath`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    
}