import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({ providedIn: 'root' })
export class WindowsServiceConfigurationService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {}
    public bmlfGetWindowServiceConfig(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetWindowServiceConfig`; 
        return this.httpExt.post<any>(url, headerData, null);
    }


    public bmlfSaveWindowServiceConfig(headerData: any) {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveWindowServiceConfig`;
            return this.httpExt.post<any>(url, headerData, null);
        }
     }
