import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { ModalService } from 'src/app/core/services/modal.service';
import * as moment from 'moment';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CustomValidator } from 'src/app/core/services/custom.validators'


@Component({
  selector: 'app-user-group-setup-report',
  templateUrl: './user-group-setup-report.component.html',
  styleUrls: ['./user-group-setup-report.component.scss']
})
export class UserGroupSetupReportComponent extends BaseGrid implements OnInit {
  public getDataList(): void {}
  public gridDataList: any;

  public msPageTitle = ''
  //public gridDataList: any = [];
  public mobjUserData: any = {};
  public mbloadingEnabled = false;
  public mbFilterEnabled:boolean = false;
  public mbLoaderEnabled:boolean = false;
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mbIsLoading: boolean = false;
  public msServeDateFormat = CommonConstants.gsServerDateFormat;
  public msScreenName = '';
  public mobjFormData:any = {}
  public msFormPath = '';
  public mbIsChange: boolean = false;
  public mbIsSaveDone: boolean = false;
  public msLookupField:any = '';
  public mbMutiSelectionLookup = true;
  public msCustomDateFormat: string = "";
  public mdtCurrentDate = new Date();
  public msProcessReportInfo: any;
  public mbProcessMsgDialog: boolean = false;
  public msMsgData: any = {};
  public mbBulkResponseView: boolean = false;
  public marrDrillDownList: any = [];
  public mbIsHyerLinkRender: boolean = false;
  public mbHyerLinkData: any = null;
  //notes
  public mbIsNotesModal: boolean = false;
  public msNotesName: string = "";
  public msNotesValue: string = "";
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};

  public msSQL:string=''
  public mArrGrid:any=[];
  public marrModalRowHeader:any=[];
  public msKeyword = '';
  public filterEnabled: boolean = false;
  public exportData;


  constructor(
    private activatedRoute: ActivatedRoute,
    private lookupService: LookupService,
    private modalservice: ModalService,
    public dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    private router: Router,
    public utilService: AppUtility,
    public toastMessageService: ToastMessageService,
    public userFieldService: UserFieldService,
  ) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
    this.msFormPath = this.activatedRoute.snapshot.routeConfig.path;
    this.msPageTitle = this.utilService.bmgfonTranslate('USERGROUPSETUPREPORT.TITLE')
    this.msScreenName = 'User Group Setup Report';

    //this.bmlfGetDrillDownList();
  }

  ngOnInit(): void {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    //this.utilService.getBMEScreePermission(this.msScreenName);
    this.mobjUserData = this.lsService.getUserSession();
    // this.bmlfOnRestData();
    // this.msSQL="";
    // this.setExportData({ 'pageName':  this.msScreenName});
    // this.exportData.translateFrom =  'USERGROUPSETUPREPORT';
     this.mobjFormData.GroupFrom='';
     this.mobjFormData.GroupFromDesc='';
     this.mobjFormData.GroupTo='';
     this.mobjFormData.GroupToDesc='';
  }


  bmlfonGlobalOnChangesFn(psFieldName, pobjFieldData, piIndex = null) {
    if (pobjFieldData == null) {
      return false;
    }
    this.mbIsChange = true;
    this.mbIsSaveDone = false;
  }

  // On rest data
  bmlfOnRestData() {
    this.mobjFormData.Company = '';
    // this.mobjFormData.CustKeyDes = '';
    // this.mobjFormData.FromDate = new Date();
    //this.mobjFormData.ToDate = new Date();
  }

  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  //Open LOOKUP MODAL
  bmlfonOpenLookupModal(psField, psEvent, psValue, psType, psEleId, piRowIdx = null) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      //this.mobjFormData[psField + 'Desc'] = '';
      this.msLookupField = '';
      return false;
    }
    
    this.mbMutiSelectionLookup = false;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjFormData[psField + 'Desc'] = '';
      return false
    }
    let psLookUpKey = this.bmlfonSetLookupKey(psField, piRowIdx);
    let pobjReqData: any = {
      ScreenName: this.msScreenName
    }
    pobjReqData.LookUpKey = psLookUpKey;

    if (psField=='GroupFrom'){
      pobjReqData={ScreenName: this.msScreenName, LookUpKey:'GroupFromSuperAdmin',Filter1: ""}
    }
    if (psField=='GroupTo'){
      pobjReqData={ScreenName: this.msScreenName, LookUpKey:'GroupToSuperAdmin',Filter1: ""}
    }

    this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.userFieldService.bmlfAdminLookupData(pobjReqData).subscribe(res => {
      setTimeout(() => {
        this.utilService.bmgfOnKeyInputEnable();
        this.mbIsLoading = false;
      }, 500);

      if (res == null) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          });
        })

        this.marrLookupGridData = res.LookupData;
        let title:string='';
        if (psField=='GroupFrom' || psField=='GroupTo'){
          title = this.utilService.bmgfonTranslate('USERGROUPSETUPREPORT.GROUPLOOKUP');
        }

        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.title = title + ' Lookup';
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue + '';
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let psInputKey = this.bmlfGetInputColumn(psField);
          let parrFilterData: any = [];
          parrFilterData = this.marrLookupGridData.filter(obj => {
            let tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : typeof obj[psInputKey] == 'string' ? obj[psInputKey].toUpperCase() : obj[psInputKey] ;
            if (tempValue == psValue.toUpperCase()) {
              return obj;
            }
          });

          if (parrFilterData.length == 1) {
            this.mobjLookupKeyData.value = parrFilterData[0]
            this.bmlfonSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else if (parrFilterData.length == 0) {
              this.mobjFormData[psField] = '';
              let elementExists = document.getElementById(psEleId);
              if (elementExists) {
                document.getElementById(psEleId).focus();
              }
              this.mbIsLookupModal = true;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.mobjFormData[psField] = '';
        if (psEleId) {
          document.getElementById(psEleId).focus();
        }
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
      }
    }, err => {
      console.log(err);
    });
  }

  //Close LOOKUP MODAL
  bmlfonCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => {
          let psFilterKey = obj[psInputKey] == 'string' ? obj[psInputKey].toUpperCase() : obj[psInputKey]
          if (psFilterKey == pobjData.searchValue.toUpperCase()) {
            return obj
          }
        });
      }
    
      if (parrFilterData.length == 0) {
        if(pobjData.field=='GroupFrom'){
          this.mobjFormData['GroupFrom'] = '';
          this.mobjFormData['GroupFromDesc'] = '';
        }
        if(pobjData.field=='GroupTo'){
          this.mobjFormData['GroupTo'] = '';
          this.mobjFormData['GroupToDesc'] = '';
        }

      }
      document.getElementById(pobjData.eleId).focus();
    }
  }

  bmlfonSaveLookupField(pobjData) {
    
    let psInputKey = this.bmlfGetInputColumn(pobjData.field);
    this.mbIsLookupModal = false;
    this.mbMutiSelectionLookup = false;
    if (!Array.isArray(pobjData.value)) {
      if (pobjData.type == 'custom') {

        if(pobjData.field=='GroupFrom'){
          this.mobjFormData['GroupFrom'] = pobjData.value[psInputKey];
          this.mobjFormData['GroupFromDesc'] = pobjData.value.Group_Description;
        }
        if(pobjData.field=='GroupTo'){
          this.mobjFormData['GroupTo'] = pobjData.value[psInputKey];
          this.mobjFormData['GroupToDesc'] = pobjData.value.Group_Description;
        }

        this.bmlfonValidateLookup(pobjData.field,pobjData.eleId);
      }
    } else {
      this.bmlfonTransferValues(pobjData.field, pobjData.value, undefined, undefined);
    }
  }

  // Set Lookup KEY
  bmlfonSetLookupKey(psField, piRowIdx) {
    if(psField=='GroupFrom'){
      return 'GroupFrom'
    }
    if(psField=='GroupTo'){
      return 'GroupTo'
    }


  }

  // Transfer Lookup Values
  bmlfonTransferValues(psFName, pobjValue, idx, eleId) {


  }

  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if(psField=='GroupFrom' || psField=='GroupTo'){
      return 'Group_ID'
    }

  }

  // Close form Modal
  bmlfCloseForm(type) {
    this.router.navigate(['/main/admin/admin']);
  }

  // Change Function
  bmlfonChange(psFieldType, psValue, psIsChange=null) {
    if (psFieldType == 'ToDate') {
      if (this.mobjFormData.ToDate < this.mobjFormData.FromDate) {
        this.mobjFormData.FromDate = this.mobjFormData.ToDate;
      }
    } else if (psFieldType == 'FromDate') {
      if (this.mobjFormData.FromDate > this.mobjFormData.ToDate) {
        this.mobjFormData.ToDate = this.mobjFormData.FromDate;
      }
    }
  }

  // GetDrillDownList
  bmlfGetDrillDownList() {
    let pobjReqData = {
      ScreenName: 'ApplyPaymentComponent',
      LookUpKey: 'DrillDwnList',
      Filter1: this.msScreenName
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.marrDrillDownList = res.LookupData;
        this.mbIsHyerLinkRender = true;
      }
    });
  }

  // DrillDown emit event
  bmgfDrillDownEvent(pobjData: any) {
    this.mbHyerLinkData = pobjData;
    if (this.mbHyerLinkData != null) {
      this.bmlfRedirectFunctionality();
    }
    // this.bmlfOnSaveDetails();
  }

  bmlfRedirectFunctionality() {
    if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.User1)) {
      let queryParamsObj = { queryParams: {} };
      queryParamsObj.queryParams[this.mbHyerLinkData.data.User2] = this.mbHyerLinkData.msField1Value;
      if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.KeyValue2)) {
        queryParamsObj.queryParams[this.mbHyerLinkData.data.User3] = this.mbHyerLinkData.msField2Value;
      }
      this.router.navigate([this.mbHyerLinkData.data.User1], queryParamsObj);
    }
  }

  bmlfOpenNotesModal(psName: string, psValue: string) {
    if (!CustomValidator.bmgfNullCheckValidator(psValue)) {
      this.mbIsNotesModal = true;
      this.msNotesName = psName;
      this.msNotesValue = psValue;
    } else {
      //Key cannot be empty
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.NOTES'));
    }
  }

  bmlfCloseNotesModal(pobjEvent: any): void {
    this.mbIsNotesModal = pobjEvent.option;
  }


  
  bmlfPrint() {
      this.mbIsLoading = true;
      let psGroupFrom='';
      let psGroupTo="zzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzz";
      if (this.mobjFormData.GroupFrom!=""){
        psGroupFrom=this.mobjFormData.GroupFrom; 
      }
      if (this.mobjFormData.GroupTo!=""){
        psGroupTo=this.mobjFormData.GroupTo; 
      }
    let parrDataList = [{
      ReportName: "User Group Setup Report",
      FromGroupID: psGroupFrom,
      ToGroupID: psGroupTo,
      FromTenantID: "", 
      ToTenantID: "zzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzz",
      ListingFlag: false,
      ListingVariable: "",
      }]
    let pobjJData = {
      DistReport: parrDataList,
      Response: [{"ResponseText": "","ErrorCode": "","Pdfpath": "" }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };
    this.userFieldService.bmlfDISTReportProcess(pobjJson).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
       
        //this.bmlfOpenErrorBlock("COMMON.MSG.SERVERERROR");
      }
      if (res.Response[0].ErrorCode == "") {
        let pDefaultReportType = localStorage.getItem('reportType');
        // this.pdfSrc = res.Response[0].Pdfpath
        // this.modalservice.open('pdfViewerModal');
        let pPDFSrc = res.Response[0].Pdfpath;
        if (pDefaultReportType == 'Print') {
          this.utilService.bmgfPrintPDF(pPDFSrc);
        } else if (pDefaultReportType == 'NewTab') {
          this.utilService.bmgfOpenPDFNewTab(pPDFSrc);
        } else {
          this.utilService.bmgfDownloadFile(pPDFSrc, this.msScreenName, pDefaultReportType);
        }
       
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    }
  ).add(() => { this.mbIsLoading = false; })
  }

 




  bmlfOnRptAction(event) {
    this.mbBulkResponseView = false;
  }

   //Open Task MODAL
   bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec:any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mobjFormData.CustKey;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  // View Help Dcoumnet
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/User Group Setup Report.pdf', '_blank',);
  }
  bmlFilterSetting(){
    this.filterEnabled = !this.filterEnabled;
  }
 public setExportData(data) {
        this.exportData = {
            "pageName": data.pageName,
            "allData": this.allData
        }
    }

    bmlfonValidateLookup(psField:any='',pId:any='') {
      if (psField == 'GroupFrom') {
        if (this.mobjFormData.GroupFrom == "") {
          this.mobjFormData.GroupFromDesc = '';
        } else if (this.mobjFormData.GroupTo != '') {
          if (this.mobjFormData.GroupFrom > this.mobjFormData.GroupTo) {
            this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('USERGROUPSETUPREPORT.VALIDATION')))
            this.mobjFormData.GroupFrom = '';
            this.mobjFormData.GroupFromDesc = '';
            setTimeout(function () { document.getElementById(pId).focus(); }, 100);
          }
        }
      }
      if (psField == 'GroupTo') {
        if (this.mobjFormData.GroupTo == "") {
          this.mobjFormData.GroupToDesc = '';
        } else if (this.mobjFormData.GroupFrom != '') {
          if (this.mobjFormData.GroupFrom > this.mobjFormData.GroupTo) {
            this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('USERGROUPSETUPREPORT.VALIDATION')))
            this.mobjFormData.GroupTo = '';
            this.mobjFormData.GroupToDesc = '';
            setTimeout(function () { document.getElementById(pId).focus(); }, 100);
          }
        }
      }
  
  
  
  
  }


}

