import { Component, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import * as moment from 'moment';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { CompanyInformationService } from '../../../http/company-information.service';
import { CompanyInformationModal } from '../../../models/company-information.model';
@Component({
    selector: 'app-company-information',
    templateUrl: './company-information.component.html'
})
export class CompanyInformationComponent implements OnInit {
    public mbIsLoading: boolean = false;
    public msPrdKey: string;
    public msScreenName: string = 'CompanyInformation';
    public mbIsChangeData: boolean = false;
    public msCustomDateFormat: string = "";
    public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
    public autoCorrect: boolean = true;
    public msFormMode: string = "I";
    /**---Object Initialize---- */
    public mobjCompanyInfoModel: CompanyInformationModal = new CompanyInformationModal();
    public mobjUserData: any = {};
    public mnumDeleteLineIndex: number;
    public mbDeleteLineItemDialog: boolean = false;
    public mbShowDeleteDialog: boolean = false;
    /**---Lookup realted field--- */
    public mbIsLookupModal: boolean = false;
    public mobjLookupKeyData: any = {};
    public marrLookupGridData: any = [];
    public msModalTitle: string = ''
    public mnumLineItemRowIndex: number = -1;
    /**---END : Lookup realted field--- */

    public msCloseActionModalName: string = "";
    public mbSaveDialogView: boolean = false;
    public mbIsSearchMode: boolean = false;
    public mbIsAddMode: boolean = false;
    public mbIsCopyMode: boolean = false;
    public mbIsDeleteMode: boolean = false;
    public mbIsCloseMode: boolean = false;
    public msCopyRecord: string = "N";

    /**-------START: TASK Field ----*/
    public mbIsTaskModal: boolean = false;
    public mobjTaskData: any = {};
    /**-------END: TASK Field ----*/


    public isDislabeFinanceSection: boolean = false;
    public mskeyValue: string = ""; //FieldExtender
    public CompanyInfoList: any = [];
    public CustomFinanceList: any = [];
    public isCheckAllreadyExcited: boolean = false;
    public mbShowIsCompanyDatabaseAldy: boolean = false;
    public mbShowDeleteDialogCheck: boolean = false;
    public mbShowOnlyAlphabet: boolean = false;
    public mbShowifCompanyIDExcited: boolean = false;
    public mbShowifAlreadyAttached: boolean = false;
    public mbdislablebutton: boolean = false;


    constructor(private route: ActivatedRoute,
        private router: Router,
        private formService: CompanyInformationService,
        private lookupService: LookupService,
        private toastMessageService: ToastMessageService,
        private dateFormatService: DateFormatService,
        public utilService: AppUtility,
        public userFieldService: UserFieldService,
        private lsService: LocalStorageService) {
    }
    ngOnInit() {

        /**--- Get default date format from service--- */
        this.msCustomDateFormat = this.dateFormatService.getDateFormat();
        this.mobjUserData = this.lsService.getUserSession();
        let psPrdKey = this.route.snapshot.queryParamMap.get('prdKey');
        let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
        this.bmlfGetDropDownLIst();



        if (CustomValidator.bmgfNullCheckValidator(psPrdKey)) {
            this.bmlfGetServerPath();
            this.msFormMode = "I";
            this.msPrdKey = "";
            this.mskeyValue = this.msPrdKey;
            let pobjCopyData = localStorage.getItem('copyCompanyInfoData');
            if (pobjCopyData) {
                let pobjGetCopyData: any = JSON.parse(pobjCopyData);
                this.mobjCompanyInfoModel = Object.assign({}, pobjGetCopyData.HeaderData);
                this.msCopyRecord = "Y";
                this.mskeyValue = pobjGetCopyData.prdKey;
                localStorage.removeItem('copyCompanyInfoData');
            }
            setTimeout(() => {
                document.getElementById("txtCompanyID").focus();
            }, 200);
        } else {

            this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
            /**----customization----- */
            this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
            this.msPrdKey = psPrdKey;
            this.mskeyValue = this.msPrdKey;
            this.bmlfGetCompanyData();
            console.log("000000000", this.msFormMode)
        }
    }


    /**--- Get Compamy Info --**/
    bmlfGetCompanyData() {
        this.mbIsLoading = true;
        let pobjOData = { HeaderData: [], Response: [] };
        pobjOData.HeaderData.push({ "CompanyId": this.msPrdKey });
        pobjOData.Response.push({
            "ResponseText": "",
            "ErrorCode": ""
        })

        let psJData = JSON.stringify(pobjOData);
        let psTempjData = JSON.stringify(psJData);
        let psJson = '{"JSON":' + psTempjData + '}';
        this.formService.bmlfGetCompanyInfo(psJson).subscribe(
            res => {
                this.mbIsLoading = false;
                if (res != null && res.HeaderData.length > 0) {
                    this.mobjCompanyInfoModel = Object.assign({}, res.HeaderData[0]);
                    if (this.mobjCompanyInfoModel.Finance_Package_DB_Type == "ACCPAC") {
                        let msParam = this.mobjCompanyInfoModel.Finance_Company_Name.split("-")
                        this.mobjCompanyInfoModel.ACCPACUserId = msParam[0];
                        this.mobjCompanyInfoModel.ACCPACPassword = msParam[1];
                    }
                    this.bmlfChangeFiancePackage("GET");
                    if (this.msCopyRecord == "Y") {
                        this.mobjCompanyInfoModel.Mfg_Company_ID = "";
                        document.getElementById("txtCompanyID").focus();
                    }
                    if (this.mobjCompanyInfoModel.Finance_Package_DB_Type == "BME" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "ACCPAC" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "GreatPlains") {
                        this.isDislabeFinanceSection = true;
                    } else {
                        this.isDislabeFinanceSection = false
                    }
                }
            }
        ).add(() => {
            this.mbIsLoading = false;
        })
    }


    /**--- Get Compamy Info --**/
    bmlfGetServerPath() {
        this.mbIsLoading = true;
        let pobjOData = {};
        let psJData = JSON.stringify(pobjOData);
        let psTempjData = JSON.stringify(psJData);
        let psJson = '{"JSON":' + psTempjData + '}';
        this.formService.bmlfGetCompanServerPath(psJson).subscribe(
            res => {
                this.mbIsLoading = false;
                if (res != null) {
                    console.log("123455555", res);
                    this.mobjCompanyInfoModel.Mfg_Server = res

                }
            }
        ).add(() => {
            this.mbIsLoading = false;
        })
    }


    bmlfGetDropDownLIst() {
        let psReqData = { ScreenName: 'MfgCompanySetup', LookUpKey: 'FinancePackage' };
        this.userFieldService.bmlfAdminGeneralSqlData(psReqData).subscribe(
            res => {
                if (res == null) {
                    this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
                    return false;
                }
                if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
                    this.CompanyInfoList = res.LookupData;
                    this.mobjCompanyInfoModel.Finance_Package_DB_Type = this.CompanyInfoList[0].Finance_Package_DB_Type;
                    if (this.mobjCompanyInfoModel.Finance_Package_DB_Type == "BME" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "ACCPAC" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "GreatPlains") {
                        this.isDislabeFinanceSection = true;
                    }
                    else {

                    } this.isDislabeFinanceSection = false;
                }
            })
    }


    bmlfCustomFinanceNameLIst(pstype = "") {
        let psReqData = { ScreenName: 'MfgCompanySetup', LookUpKey: 'CustomFinanceName', Filter1: this.mobjCompanyInfoModel.Finance_Package_DB_Type };
        this.userFieldService.bmlfAdminGeneralSqlData(psReqData).subscribe(
            res => {
                if (res == null) {
                    this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
                    return false;
                }
                if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
                    this.CustomFinanceList = res.LookupData;
                    if (pstype == "GET") {
                        let psvalue = this.mobjCompanyInfoModel.CustomFinanceName;
                        this.mobjCompanyInfoModel.CustomFinanceName = "";
                        setTimeout(() => {
                            this.mobjCompanyInfoModel.CustomFinanceName = psvalue;
                        }, 500);
                    }
                }
            })
    }


    bmlfChangeFiancePackage(pstype = "") {
        // this.mbIsChangeData=true;
        if (this.mobjCompanyInfoModel.Finance_Package_DB_Type == "BME" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "ACCPAC" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "GreatPlains") {
            this.isDislabeFinanceSection = true;
        } else {
            this.isDislabeFinanceSection = false
        }
        this.bmlfCustomFinanceNameLIst(pstype);
    }

    /**-------- Company Id Validation start ---*/

    bmlfOnCompanyIDChange() {
        this.bmlfCheckSpecialCharater(); //For Special Charater check
        this.bmlfCheckCompanyExistance();  // FbmlfCheckCompanyExistanceor Check save 
        if (this.mobjCompanyInfoModel.Mfg_Company_ID) {
            this.mobjCompanyInfoModel.Mfg_Company_DB_Name = this.mobjCompanyInfoModel.Mfg_Company_ID;
        }
    }

    bmlfCheckSpecialCharater() {
        var format = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/;
        if (format.test(this.mobjCompanyInfoModel.Mfg_Company_ID)) {
            this.mbShowOnlyAlphabet = true;
        } else {
            this.mbShowOnlyAlphabet = false;
        }
    }

    bmlfCheckCompanyExistance() {
        let psReqData = { ScreenName: 'MfgCompanySetup', LookUpKey: 'CompanyExistance', Filter1: this.mobjCompanyInfoModel.Mfg_Company_ID };
        this.userFieldService.bmlfAdminGeneralSqlData(psReqData).subscribe(res => {
            if (res.LookupData.length != 0) {
                this.mbShowifCompanyIDExcited = true;
            } else {
                this.mbShowifCompanyIDExcited = false;
            }
        })
    }

    bmlfOnFormFieldChange() {

        this.bmlfRemoveFinanceSection();
        this.mbIsChangeData = true;
        if (this.mobjCompanyInfoModel.Finance_Package_DB_Type == "BME" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "ACCPAC" || this.mobjCompanyInfoModel.Finance_Package_DB_Type == "GreatPlains") {
            this.isDislabeFinanceSection = true;
        } else {
            this.isDislabeFinanceSection = false
        }

        if (this.msFormMode != 'U') { // Check for company Attacted 
            let psReqData_AlreadyAttached = { ScreenName: 'MfgCompanySetup', LookUpKey: 'AlreadyAttached', Filter1: this.mobjCompanyInfoModel.Mfg_Company_DB_Name };
            this.userFieldService.bmlfAdminGeneralSqlData(psReqData_AlreadyAttached).subscribe(res => {
                if (res.LookupData.length != 0) {
                    this.mbShowifAlreadyAttached = true;
                    this.mbdislablebutton = true
                } else {
                    this.mbShowifAlreadyAttached = false;
                    this.mbdislablebutton = false
                }
            })
        }
    }

    bmlfOnChangeFinInterface() {
        this.bmlfCustomFinanceNameLIst();
        //   this.bmlfChangeFiancePackage("GET");
    }
    bmlfRemoveSpecialCharacter() {
        this.mobjCompanyInfoModel.Mfg_Company_ID = '';
        this.mobjCompanyInfoModel.Mfg_Company_DB_Name = '';
        this.mbShowOnlyAlphabet = false;
        document.getElementById("txtCompanyID").focus();
    }

    bmlfRemoveAlreadyExcitedId() {
        this.mobjCompanyInfoModel.Mfg_Company_ID = '';
        this.mobjCompanyInfoModel.Mfg_Company_DB_Name = '';
        this.mbShowifCompanyIDExcited = false;
        document.getElementById("txtCompanyID").focus();
    }

    /**---------End Company Id Validation start --*/


    bmlfOpenErrorBlock(psMsg): void {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
    }


    /**--------Start: Lookup Functionality----- */
    // LOOKUP MODAL 
    bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
        this.mbIsLoading = true;
        this.marrLookupGridData = [];
        this.mobjLookupKeyData = {};
        this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
        if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
            this.mobjCompanyInfoModel[psField] = "";
            this.mbIsLoading = false;
            return false
        }
        let psReqData = {
            ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: "", Filter2: "", Filter3: "", Filter4: '', Filter5: ''
        };

        this.userFieldService.bmlfAdminLookupData(psReqData).subscribe(res => {
            this.mbIsLoading = false;
            if (res == null) {
                this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
                return false;
            }
            if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
                res.LookupData.forEach((ele) => {
                    Object.keys(ele).forEach((key) => {
                        let psKey = key.toUpperCase();
                        if (psKey.search('DATE') != -1) {
                            if (ele[key] && ele[key] != '') {
                                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
                            } else {
                                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
                            }
                        }
                    });
                })

                if (psField != 'TenantID') {
                    this.marrLookupGridData = res.LookupData.map(({ Mfg_Company_ID, Mfg_Company_Name, Mfg_Server, Mfg_Company_DB_Name, Finance_Package_DB_Type, Finance_Company_Name, ...rest }) => ({ 'CompanyID': Mfg_Company_ID, 'Company Name': Mfg_Company_Name, 'Server/Path': Mfg_Server, 'Database File': Mfg_Company_DB_Name, 'Finance Package': Finance_Package_DB_Type, 'Finance Company Name': Finance_Company_Name, ...rest }));
                } else {
                    this.marrLookupGridData = res.LookupData;
                }
                this.mnumLineItemRowIndex = pnumRowIdx;
                this.mobjLookupKeyData.field = psField;
                this.mobjLookupKeyData.gridData = this.marrLookupGridData;
                this.mobjLookupKeyData.type = psType;
                this.mobjLookupKeyData.eleId = psEleId;

                if (psValue) {
                    this.mobjLookupKeyData.searchValue = psValue.toString();
                } else {
                    this.mobjLookupKeyData.searchValue = '';
                }
                if (psEvent == 'blur' && psValue) {
                    let parrFilterdArr: any = [];
                    /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
                    let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
                    parrFilterdArr = this.marrLookupGridData.filter(obj => {
                        if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
                            return obj;
                        }
                    });
                    if (parrFilterdArr.length == 1) {
                        this.mobjLookupKeyData.value = parrFilterdArr[0];
                        this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
                        this.bmlfSaveLookupField(this.mobjLookupKeyData);
                        this.mbIsLookupModal = false;
                    } else {
                        this.mbIsLookupModal = true;
                    }
                } else if (psEvent == 'click') {
                    this.mbIsLookupModal = true;
                }

            } else {
                this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
            }
        }, err => {
            console.log(err);
            this.mbIsLoading = false;
        })
    }

    /** save lookup field data */
    bmlfSaveLookupField(pobjEvent) {
        this.mbIsLoading = false;
        this.mbIsLookupModal = false;
        if (pobjEvent.type == 'custom') {
            if (pobjEvent.field == "CmpInfoList") {
                this.bmlfOpenSelectedRecord(pobjEvent.value);
            } else if (pobjEvent.field == "TenantID") {
                this.mobjCompanyInfoModel[pobjEvent.field] = pobjEvent.valueKey;
                this.bmlfCheckDatabasefileExist();
            }
        }
    }


    // get Lookup Value Column
    bmlfGetInputColumn(psField) {
        if (psField == 'TenantID') {
            return 'TenantID';
        }
    }


    //Close LOOKUP MODAL
    bmlfCloseLookupModal(pobjData) {
        this.mbIsLookupModal = pobjData.isLookupModal;
        if (pobjData.eleId != null) {
            let psInputKey = this.bmlfGetInputColumn(pobjData.field);
            let parrFilterData: any = [];
            if (psInputKey) {
                parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
            }
            if (parrFilterData.length == 0) {
                this.mobjCompanyInfoModel[pobjData.field] = '';
                // this.mbIsChangeData = true;
            }
            document.getElementById(pobjData.eleId).focus();
        }
    }


    /**--------End: Lookup Functionality----- */


    bmlfCheckDatabasefileExist() {
        //   this.bmlfOnFormFieldChange();
        if (this.msFormMode != 'U') {
            let psReqData_DBExistance = { ScreenName: 'MfgCompanySetup', LookUpKey: 'DBExistance', Filter1: this.mobjCompanyInfoModel.Mfg_Company_DB_Name };
            this.userFieldService.bmlfAdminGeneralSqlData(psReqData_DBExistance).subscribe(res => {
                if (res.LookupData.length != 0) {
                    this.isCheckAllreadyExcited = true;
                    return true
                } else {
                    this.isCheckAllreadyExcited = false;
                    return false
                }
            })
        }
    }



    bmlfRemoveAlreadyAttached() {
        this.mbShowifAlreadyAttached = false;
        return false;
    }


    bmlCompanyDatabaseAldy(psType) {
        if (psType == 'No') {
            this.mbShowIsCompanyDatabaseAldy = false;
        } else {
            this.mbShowIsCompanyDatabaseAldy = false;
            this.bmlfSaveProductData();
        }
    }


    /**--------End: Lookup Functionality----- */
    bmlfValidData() {
        if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Mfg_Company_ID)) {
            this.bmlfOpenErrorBlock("ConpanyInfo.MSG.COMPANYID");
            return false;
        } else if (this.mobjCompanyInfoModel.Mfg_Company_ID.includes(" ")) {
            this.bmlfOpenErrorBlock("ConpanyInfo.MSG.SPACENOTALLOWED");
            return false;
        } else if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Mfg_Company_DB_Name)) {
            this.bmlfOpenErrorBlock("ConpanyInfo.MSG.DATABASEFILE");
            return false;
        } else if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.TenantID)) {
            this.bmlfOpenErrorBlock("ConpanyInfo.MSG.TENENTID");
            return false;
        } else if (this.isCheckAllreadyExcited == true) {
            this.mbShowIsCompanyDatabaseAldy = true;
            return false;
        } else if ((this.mobjCompanyInfoModel.FinInterFace == true) && ((this.mobjCompanyInfoModel.Finance_Package_DB_Type != "BME") && (this.mobjCompanyInfoModel.Finance_Package_DB_Type != "ACCPAC") && (this.mobjCompanyInfoModel.Finance_Package_DB_Type != "GreatPlains"))) {
            if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Finance_Package_DB_Type)) {
                this.bmlfOpenErrorBlock("ConpanyInfo.MSG.FINANCE_PACAGE");
                return false;
            } else if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.CustomFinanceName)) {
                this.bmlfOpenErrorBlock("ConpanyInfo.MSG.CUSTOMERFINACE");
                return false;
            } else if (this.mobjCompanyInfoModel.CustomFinanceName == 'QuickBooks Desktop' && CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Finance_DSN)) {
                this.bmlfOpenErrorBlock("ConpanyInfo.MSG.FINANCE_DSN");
                return false;
            } else {
                return true;
            }
        } else if ((this.mobjCompanyInfoModel.FinInterFace == true) && (this.mobjCompanyInfoModel.Finance_Package_DB_Type == 'GreatPlains' || this.mobjCompanyInfoModel.Finance_Package_DB_Type == 'ACCPAC')) {

            if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Finance_Company_Server)) {
                this.bmlfOpenErrorBlock("Enter Company Server in Finance Company Information Section");
                return false;

            } else if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Finance_Company_ID)) {
                this.bmlfOpenErrorBlock("Enter company ID in Finance Company Information Section");
                return false;

            } else if (this.mobjCompanyInfoModel.Finance_Package_DB_Type == 'GreatPlains' && CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Finance_Company_Name)) {
                this.bmlfOpenErrorBlock("ConpanyInfo.MSG.SySADMINNAME");
                return false;
            } else {

                return true;
            }


        } else {
            return true;
        }
    }


    bmlfSubmitData(pbClose: boolean = false): void {
        if (this.mbIsChangeData == false && (this.msFormMode == 'U')) {
            return;
        }
        if (!this.bmlfValidData()) {
            return;
        }
        this.bmlfSaveProductData();
    }

    bmlfSaveProductData(): void {
        this.mbIsLoading = true;
        //SCR 2335-1020 Villa Dolce Gelato
        this.mobjCompanyInfoModel.Finance_Package_DB_Type = this.mobjCompanyInfoModel.FinInterFace == false ? 'None' : this.mobjCompanyInfoModel.Finance_Package_DB_Type
      
        if(this.isDislabeFinanceSection){
            this.mobjCompanyInfoModel.Finance_Company_ID= this.mobjCompanyInfoModel.Mfg_Company_ID
            this.mobjCompanyInfoModel.Finance_Company_Name= this.mobjCompanyInfoModel.Mfg_Company_ID
        }

        this.mobjCompanyInfoModel.Finance_DSN = this.mobjCompanyInfoModel.Finance_Package_DB_Type 
      
        let parrHeaderData = [];
        parrHeaderData.push(Object.assign({}, this.mobjCompanyInfoModel));
        let pobjJData = {
            HeaderData: parrHeaderData,
            Response: [{
                "ResponseText": "",
                "ErrorCode": ""
            }]
        };
        let pobjJson = {
            JSON: JSON.stringify(pobjJData)
        };

        console.log("pobjJson",pobjJson)

        this.formService.bmlfSaveCompanyInfo(pobjJson, this.msFormMode).subscribe(
            res => {
                this.mbIsChangeData = false;
                this.mbIsLoading = false;
                if (res.Response[0].ErrorCode == "") {

                    this.mbIsChangeData = false;
                    if (this.msFormMode == "I") {
                        this.msFormMode = "U";
                        this.msCopyRecord = "N";
                        this.mobjCompanyInfoModel.Mfg_Company_ID = res.HeaderData[0].Mfg_Company_ID;
                        this.msPrdKey = this.mobjCompanyInfoModel.Mfg_Company_ID;
                        this.bmlfCheckDatabasefileExist()
                        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate(res.Response[0].ResponseText));
                    } else if (this.msFormMode == "U") {
                        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
                        this.bmlfCheckDatabasefileExist()
                        this.mbIsChangeData = false;
                    }


                    // FieldExtender -- End update
                    if (this.mbIsSearchMode) {
                        this.mbIsSearchMode = false;
                        this.bmlfOpenLookupModal('CmpInfoList', this.msScreenName, 'CmpInfoList', 'click', '', 'custom', 'cmdSearch', 'ConpanyInfo.TITLE');
                    } else if (this.mbIsAddMode) {
                        this.mbIsAddMode = false;
                        this.bmlfRefreshForm();
                    } else if (this.mbIsCopyMode) {
                        this.mbIsCopyMode = false;
                        this.bmlfCopyData();
                    } else if (this.mbIsCloseMode) {
                        this.mbIsCloseMode = false;
                        this.router.navigate(["/main/admin/company-information-list"]);
                    } else {
                        this.bmlfGetCompanyData();
                    }
                    this.mbIsChangeData = false;
                } else {
                    this.bmlfCheckDatabasefileExist()
                    this.mbIsChangeData = false;
                    this.mbIsSearchMode = false;
                    this.mbIsAddMode = false;
                    this.mbIsCopyMode = false;
                    this.mbIsDeleteMode = false;
                    this.mbIsCloseMode = false;
                    this.toastMessageService.notifyError(res.Response[0].ResponseText)
                }
                console.log(res);
            }
        ).add(() => { this.mbIsLoading = false; })
    }


    bmlfCloseCompanyInfo(): void {

        if (this.mbIsChangeData == true) {
            this.mbSaveDialogView = true;
            this.mbIsCloseMode = true;
        } else {
            this.router.navigate(["/main/admin/company-information-list"]);
        }
    }


    bmlfCheckdatabase() {
        this.mbIsLoading = true;
        var check = false;

        let HeaderData = [];
        HeaderData = [{
            Mfg_Server: this.mobjCompanyInfoModel.Mfg_Server,
            Mfg_Company_DB_Name: this.mobjCompanyInfoModel.Mfg_Company_DB_Name,
            RecordCount: ''
        }]
        let reqData: any = {
            JSON: JSON.stringify({
                HeaderData: HeaderData,
                Response: [{ "ResponseText": "", "ErrorCode": "" }]
            })
        };

        this.formService.bmlfGetComDataForDelete(reqData).subscribe(res => {
            if (res == null) {
                this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
                return false;
            }
            if (res.Response[0].ErrorCode == "") {
                this.mbIsLoading = false;
                {
                    if (res.HeaderData[0].RecordCount > 0) {
                        check = true
                    } else {
                        check = false
                    }
                }
            } else {
                this.mbIsLoading = false;
            }
        })
        return check;
    }


    // So Footer Action finction
    bmlfFooterActionFn(event) {
        if (event == 'searchEvn') {
            if (this.mbIsChangeData) {
                this.mbSaveDialogView = true;
                this.mbIsSearchMode = true
                return false;
            }
            this.bmlfOpenLookupModal('CmpInfoList', this.msScreenName, 'CmpInfoList', 'click', '', 'custom', 'cmdSearch', 'ConpanyInfo.TITLE');
        } else if (event == 'addEvn') {

            if (!this.bmlfValidData()) {
                return;
            }
            if (this.mbIsChangeData) {
                this.mbSaveDialogView = true;
                this.mbIsAddMode = true
                return false;
            }
            this.bmlfRefreshForm();
        } else if (event == 'deleteEvn') {

            if (this.mobjCompanyInfoModel.Mfg_Company_ID == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbIsChangeData) {
                this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
                return false;
            }

            if (this.mobjCompanyInfoModel.Mfg_Company_ID == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbIsChangeData) {
                this.bmlfRefreshForm();
                return false;
            }

            var mbCheckDeleteDatabase = this.bmlfCheckdatabase();
            if (mbCheckDeleteDatabase) {
                this.mbShowDeleteDialogCheck = true;
            } else {
                this.mbShowDeleteDialog = true;
            }

        } else if (event == 'copyEvn') {

            if (CustomValidator.bmgfNullCheckValidator(this.mobjCompanyInfoModel.Mfg_Company_ID)) {
                this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate("COMMON.MSG.COPY"));
                document.getElementById("txtCompanyID").focus();
                return;
            }
            if (this.mbIsChangeData) {
                this.mbSaveDialogView = true;
                this.mbIsCopyMode = true;
                return false;
            }
            this.bmlfCopyData();
        }
    }


    bmlfOnConfirmModal(psype) {
        if (psype == 'YES') {
            this.bmlfSubmitData();
            this.mbSaveDialogView = false;
        } else if (psype == 'NO') {
            this.mbSaveDialogView = false;
            if (this.mbIsSearchMode) {
                this.mbIsSearchMode = false;
                this.bmlfOpenLookupModal('CmpInfoList', this.msScreenName, 'CmpInfoList', 'click', '', 'custom', 'cmdSearch', 'ConpanyInfo.TITLE');
            } else if (this.mbIsAddMode) {
                this.mbIsAddMode = false;
                this.bmlfRefreshForm();
            } else if (this.mbIsCopyMode) {
                this.mbIsCopyMode = false;
                this.bmlfCopyData();
            } else if (this.mbIsDeleteMode) {
                this.mbIsDeleteMode = false;
                this.mbShowDeleteDialog = true;
            }
            else {
                this.router.navigate(['/main/admin/company-information-list']);
            }
        } else {
            this.mbSaveDialogView = false;
            this.mbIsSearchMode = false;
            this.mbIsCopyMode = false;
            this.mbIsAddMode = false;
            this.mbIsCloseMode = false;
        }
    }

    // Delete Sales Order Dialog events
    bmlfonDialogAction(psAction) {
        if (psAction == 'Cancel') {
            this.mbShowDeleteDialog = false;
        } else if (psAction == 'Yes' || psAction == 'No') {
            if (this.msFormMode == "I") {
                this.bmlfRefreshForm();
                return false;
            }
            this.mbIsLoading = true;
            let HeaderData = [];
            HeaderData = [{
                Mfg_Company_ID: this.mobjCompanyInfoModel.Mfg_Company_ID,
                Mfg_Server: this.mobjCompanyInfoModel.Mfg_Server,
                Mfg_Company_DB_Name: this.mobjCompanyInfoModel.Mfg_Company_DB_Name,
                DeleteSQLDataBase: psAction == 'Yes' ? 'YES' : psAction
            }]
            let reqData: any = {
                JSON: JSON.stringify({
                    HeaderData: HeaderData,
                    Response: [{
                        "ResponseText": "", "ErrorCode": ""
                    }]
                })
            };
            this.formService.bmlfDeleteCompanyInfo(reqData).subscribe(res => {
                if (res == null) {
                    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
                    return false;
                }
                if (res.Response[0].ErrorCode == "") {
                    {
                        this.mbIsLoading = false;
                        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
                        this.mbShowDeleteDialog = false;
                        this.bmlfRefreshForm();
                    }
                } else {
                    this.toastMessageService.notifyError(res.Response[0].ResponseText);
                    this.mbShowDeleteDialog = false;
                    this.mbIsLoading = false;
                }
            })
        }
    }

    bmlfOpenSelectedRecord(data) {
        let param = { prdKey: data.CompanyID };
        this.utilService.bmgfRefreshReuse('/main/admin/company-information', true, param);
    }

    // Refresh Sales Order
    bmlfRefreshForm() {
        this.utilService.bmgfRefreshReuse('/main/admin/company-information', false);
    }

    // Copy Current WHT Data
    bmlfCopyData() {
        let HeaderData = Object.assign({}, this.mobjCompanyInfoModel);
        HeaderData.Mfg_Company_ID = "";
        let parrCopyData = {
            HeaderData: HeaderData,
            prdKey: this.mobjCompanyInfoModel.Mfg_Company_ID
        }
        localStorage.setItem('copyCompanyInfoData', JSON.stringify(parrCopyData));
        this.bmlfRefreshForm();
    }

    //Open Task MODAL 
    bmlfOpenTaskModal(taskType) {
        this.mobjTaskData = {};
        this.mobjTaskData.taskType = taskType;
        if (taskType == 'myTask') {
            this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
            this.mobjTaskData.innerRecord = this.mobjUserData;
        } else if (taskType == 'recordTask') {
            this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
            let pobjFormRec: any = {};
            pobjFormRec.formName = this.msScreenName;
            pobjFormRec.primeKey = this.mobjCompanyInfoModel.Mfg_Company_ID;
            this.mobjTaskData.innerRecord = pobjFormRec;
        }
        this.mbIsTaskModal = true;
    }

    //Close Task MODAL 
    bmlfonCloseTaskModal(pobjData) {
        this.mbIsTaskModal = pobjData.isTaskModal;
    }


    bmlfViewHelpDoc() {
        window.open('../../../../assets/help files/Mfg Company Setup.pdf', '_blank');
    }



    bmlfRemoveFinanceSection() {
        if (this.msFormMode != 'U' && this.mobjCompanyInfoModel.FinInterFace == false) {
            this.mobjCompanyInfoModel.Finance_Company_ID = '';
            this.mobjCompanyInfoModel.Finance_Company_Name = '';
            this.mobjCompanyInfoModel.Finance_DSN = '';
            this.mobjCompanyInfoModel.Finance_UserId = '';
            this.mobjCompanyInfoModel.Finance_Pwd = '';
        }
    }




}