  bmlfonValidateLookup(psField:any='',pId:any='') {
    if (psField == 'UserFrom') {
      if (this.mobjFormData.UserFrom == "") {
        this.mobjFormData.UserFromDesc = '';
      } else if (this.mobjFormData.UserTo != '') {
        if (this.mobjFormData.UserFrom > this.mobjFormData.UserTo) {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('USERACTIVITYLOG.USERVALIDATION')))
          this.mobjFormData.UserFrom = '';
          this.mobjFormData.UserFromDesc = '';
          setTimeout(function () { document.getElementById(pId).focus(); }, 100);
        }
      }
    }
    if (psField == 'UserTo') {
      if (this.mobjFormData.UserTo == "") {
        this.mobjFormData.UserToDesc = '';
      } else if (this.mobjFormData.UserFrom != '') {
        if (this.mobjFormData.UserFrom > this.mobjFormData.UserTo) {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('USERACTIVITYLOG.USERVALIDATION')))
          this.mobjFormData.UserTo = '';
          this.mobjFormData.UserToDesc = '';
          setTimeout(function () { document.getElementById(pId).focus(); }, 100);
        }
      }
    }   
    
}

//from to date change
bmlfonFromChange(value: Date) {
  if((this.mobjFormData.DateTo != null) || (value != null)){
    let pbResult: boolean = true;
    pbResult = CustomValidator.bmgfCompareTwoDates(value, this.mobjFormData.DateTo);
    if (pbResult == false) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('USERACTIVITYLOG.DATEVALIDATION'));
      //this.mobjFormData.DateFrom = getToday();
      return false;
    }
  }
  
}

bmlfonToChange(value: Date) {
  if((this.mobjFormData.DateFrom != null) || (value != null)){
    let pbResult: boolean = true;
    pbResult = CustomValidator.bmgfCompareTwoDates(this.mobjFormData.DateFrom, value);
    if (pbResult == false) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('USERACTIVITYLOG.DATEVALIDATION'));
      //this.mobjFormData.DateTo = getToday();
      return false;
    }
  }
  
}


