import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { Router } from '@angular/router';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { UserSetupService } from '../../../http/user-setup.service';
@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss']
})
export class UserListComponent extends BaseGrid implements OnInit {
  public bmlfSelectedCallback = (args) => args.dataItem;
  public gridDataList = [];
  public marrOrginalGridList = [];
  public mbLoadingEnabled = false;
  public msKeyword = '';
  public mobjGridSettings: GridSettings = new GridSettings();
  public marrFilterList = [];
  public msCustomDateFormat: string = "";
  public msScreenName: string = "User Setup";
  public mbShowDeleteDialog: boolean = false;
  public msMsgData: any = {};
  public mbBulkDeleteView: boolean = false;
  public mbBulkDelete: boolean = false;
  public mObjModel: any = {};
  public marraySelected: Array<any> = [];
  public mbIsLoading = false;
  public mobjUserData: any = {};
  public TennatId: string = "01";
  public msAdminType:string='';
  public msTenantId:string='';
  constructor(private router: Router,
    private lsService: LocalStorageService, private formService: UserSetupService,
    public dateFormatService: DateFormatService,
    public toastMessageService: ToastMessageService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService,
  ) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
  }

  ngOnInit() {
    
    this.msAdminType = localStorage.getItem("isLoginType");
    this.msTenantId=this.localStorageService.getUserSession().CompanyList[0]['TenantId'];
    this.mobjUserData = this.lsService.getUserSession();
    this.TennatId = this.mobjUserData.TenantId;
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    //this.utilService.getBMEScreePermission(this.msScreenName);
    this.initGrid();
    this.getDataList();
    this.setExportData({ 'pageName': this.msScreenName });
    this.exportData.translateFrom = '';
  }

  getDataList(): void {
    this.mbLoadingEnabled = true;
    let pobjHeaderData;
    if(this.msAdminType=="Super Admin"){
      pobjHeaderData = { "ScreenName": this.msScreenName, "LookUpKey": "UserSetupListWithoutTenant", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }
    }else{
      pobjHeaderData = { "ScreenName": this.msScreenName, "LookUpKey": "UserSetupList", "Filter1": this.TennatId, "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }
    }
     //pobjHeaderData = { "ScreenName": this.msScreenName, "LookUpKey": "UserSetupList", "Filter1": this.TennatId, "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }
    this.userFieldService.bmlfAdminLookupData(pobjHeaderData).subscribe(
      res => {
        let parrGridData = res.LookupData;
        // parrGridData.forEach(ele => {
        //     ele.Recdate = new Date(ele.Recdate);
        // })
        this.gridDataList = parrGridData;
        this.marrOrginalGridList = parrGridData;
        let parrSettingData = "";
        if (res.GridSettingData.length > 0) {
          parrSettingData = res.GridSettingData[0].JSONString;
        }
        this.setInitGridSettings(parrSettingData, "USERSETUP");
        //NG remove Tenant ID column if User is Tenant User
        if(this.msTenantId != '00'){
          const indexOfObject = (this.mobjGridSettings.columnsConfig).findIndex(object => {
            return object.field == 'TenantID';
          });
          (this.mobjGridSettings.columnsConfig).splice(indexOfObject, 1);
        }
        //NG remove Tenant ID column if User is Tenant User
        this.exportData.columnsConfig = this.mobjGridSettings.columnsConfig;
        this.mobjGridSettings.state.take=50;
      }, error => {
        console.log(error);
      }
    ).add(() => { this.mbLoadingEnabled = false })

  }

  public bmlfResetGridSettings() {
    this.bmlfOnAdminResetGridSettings(this.msScreenName, "USERSETUP");
  }

  public bmlfSaveGridSetting(): void {
    this.bmlfOnAdminSaveGrid(this.msScreenName);
  }
  bmlfonAdd(): void {
    //check Write permission
    // if (!this.utilService.mbWrite) {
    //     this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
    //     return;
    // };
    if(this.msAdminType=="Super Admin"){
      this.router.navigate(['/main/admin/userlist/manage']);
    }else{
      this.router.navigate(['/main/admin/user/manage']);
    }
    
  }
  bmlfonEdit(data) {
    
    if(this.msAdminType=="Super Admin"){
      this.router.navigate(['/main/admin/userlist/manage'], {
        queryParams: {
          userId: data.User_ID, TenantID: data.TenantID
        }
      });
    }else{ 
      this.router.navigate(['/main/admin/user/manage'], {
        queryParams: {
          userId: data.User_ID
        }
      });
    }
  }
  bmlfonCopy(data) {
    //check Write permission
    // if (!this.utilService.mbWrite) {
    //     this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
    //     return;
    // };
    if(this.msAdminType=="Super Admin"){
      this.router.navigate(['/main/admin/userlist/manage'], { queryParams: { userId: data.User_ID, TenantID: data.TenantID, CopyRecord: "Y" }, skipLocationChange: true });
    }else{
      this.router.navigate(['/main/admin/user/manage'], { queryParams: { userId: data.User_ID, CopyRecord: "Y" }, skipLocationChange: true });
    }
    

  }
  //Delete record
  bmlfonDelete(data) {
    //check delete permission
    // if (!this.utilService.mbDelete) {
    //     this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
    //     return;
    // };
    this.mObjModel = data;
    this.mbShowDeleteDialog = true;
  }

  //Bulk Delete
  onActionDelete() {
    //check delete permission
    // if (!this.utilService.mbDelete) {
    //     this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
    //     return;
    // };
    if (this.marraySelected.length == 0) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'))
      return;
    }
    this.mbBulkDelete = true;
    this.mbShowDeleteDialog = true;
  }
  bmlfonDialogAction(action) {
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      let parrHeaderData = [];
      if (this.mbBulkDelete == true) {
        if(this.msAdminType=="Super Admin"){
          this.marraySelected.forEach(x => { parrHeaderData.push({ User_Id: x.User_ID, TenantID: x.User_ID.substring(0,2) })})
        }else{ 
          this.marraySelected.forEach(x => { parrHeaderData.push({ User_Id: x.User_ID, TenantID: this.TennatId })})
        }
      }
      else {
        if(this.msAdminType=="Super Admin"){
          parrHeaderData = [{ User_Id: this.mObjModel.User_ID, TenantID: this.mObjModel.User_ID.substring(0,2) }] 
        }else{ 
         parrHeaderData = [{ User_Id: this.mObjModel.User_ID, TenantID: this.TennatId }] 
        }
      }


      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: parrHeaderData,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteUser(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          if (this.mbBulkDelete == true) {
            this.msMsgData.Data = res.Response[0].ResponseText;
            this.msMsgData.Header = this.utilService.bmgfonTranslate('COMMON.MSG.BATCHMASTER');
            this.msMsgData.mbBulkDeleteView = true;
            this.mbBulkDeleteView = true;
          } else {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
          }
          this.marraySelected = [];
          this.mbShowDeleteDialog = false;
          this.mbBulkDelete = false;
          this.getDataList();

        } else {
          if (this.mbBulkDelete == true) {
            this.msMsgData.Data = res.Response[0].ResponseText;
            this.msMsgData.Header = this.utilService.bmgfonTranslate('COMMON.MSG.BATCHMASTER');
            this.msMsgData.mbBulkDeleteView = true;
            this.mbBulkDeleteView = true;
            this.marraySelected = [];
            this.mbBulkDelete = false;
            this.getDataList();
          } else {
            this.toastMessageService.notifyError(res.Response[0].ResponseText);
          }
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }
  bmlfonselectionChange(e) {
    if (e.selectedRows.length > 0) {
      e.selectedRows.forEach(ele => {
        let parrfilterData = this.marraySelected.filter(x => x.User_ID == ele.dataItem.User_ID);
        if (parrfilterData == undefined || parrfilterData.length == 0) {
          this.marraySelected.push(ele.dataItem);
        }
      })
    }
    else if (e.deselectedRows.length > 0) {
      e.deselectedRows.forEach(ele => {
        let pobjTempData = ele.dataItem;
        this.marraySelected = this.marraySelected.filter(x => {
          if (x.User_ID != pobjTempData.User_ID) {
            return x;
          }
        })
      })
    }
  }
  bmlfonCloseModal(event) {
    this.mbBulkDeleteView = event.mbBulkDeleteView;
  }

  bmlFilterSetting() {
    this.filterEnabled = !this.filterEnabled;
  }

}
