import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { UserSetupService } from '../../../http/user-setup.service';
import { UserSetupModal } from '../../../models/user.model';
import * as moment from 'moment';

@Component({
  selector: 'app-user-manage',
  templateUrl: './user-manage.component.html',
  styleUrls: ['./user-manage.component.scss']
})
export class UserManageComponent implements OnInit {
  public mbIsLoading: boolean = false;
  public msUserKey: string;
  public msScreenName: string = 'User Setup';
  public mbIsChangeData: boolean = false;
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public autoCorrect: boolean = true;
  public msFormMode: string = "I";
  public mdateMinExpire: Date = new Date();
  /**---Object Initialize---- */
  public mobjUserModel: UserSetupModal = new UserSetupModal();
  public mobjUserData: any = {};
  public marrCompanyList: any = [];

  public mbSaveDialogView: boolean = false;
  public mbIsSearchMode: boolean = false;
  public mbIsAddMode: boolean = false;
  public mbIsCopyMode: boolean = false;
  public mbIsDeleteMode: boolean = false;
  public mbIsCloseMode: boolean = false;
  public msCopyRecord: string = "N";
  public mnumDeleteLineIndex: number;
  public mbDeleteLineItemDialog: boolean = false;
  public mbShowDeleteDialog: boolean = false;
  public msImgURL: any = "../../../../assets/images/user.png";
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModalTitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  public msLookupField = '';
  public mbMutiSelectionLookup: boolean = false;
  public mbIsQuantityLookup: boolean = false;
  /**---END : Lookup realted field--- */
  public msTenenatId: string = "01";
  public msStartUserId: string = "";
  public msAdminType: string = '';
  public msTenantIdSearch: string = '';
  public msGroupType: string = "";
  constructor(private route: ActivatedRoute,
    private router: Router,
    private formService: UserSetupService,
    private lookupService: LookupService,
    private toastMessageService: ToastMessageService,
    private dateFormatService: DateFormatService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService, private lsService: LocalStorageService) { }

  ngOnInit(): void {

    this.msAdminType = localStorage.getItem("isLoginType")
    /*--------- get screen permission --------*/
    //this.utilService.getBMEScreePermission(this.msScreenName);

    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    if (this.msAdminType == "Super Admin") {
      if (this.route.snapshot.queryParamMap.get('TenantID') == null) { //search
        this.msTenenatId = localStorage.getItem("TenantIdSearchLookup");
      } else { //list page      
        this.msTenenatId = this.route.snapshot.queryParamMap.get('TenantID');
      }
    } else {
      this.msTenenatId = this.mobjUserData.TenantId;
    }
    let psUserKey = this.route.snapshot.queryParamMap.get('userId');
    let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
    if (CustomValidator.bmgfNullCheckValidator(psUserKey)) {
      this.msFormMode = "I";
      this.msUserKey = "";
      this.msStartUserId = this.msTenenatId;
      if (this.msAdminType == "Super Admin") {
        this.msTenenatId = localStorage.getItem("TenantIdWise");
        this.msStartUserId = this.msTenenatId;
      }
      let pobjCopyData = localStorage.getItem('copyUserData');
      if (pobjCopyData) {
        let pobjGetCopyData: any = JSON.parse(pobjCopyData);
        let pobjHeadData = Object.assign({}, pobjGetCopyData.HeaderData);
        pobjHeadData.UserDOB = pobjHeadData.UserDOB == null ? null : new Date(pobjHeadData.UserDOB);
        pobjHeadData.Password_Expire_Date = pobjHeadData.Password_Expire_Date == null ? null : new Date(pobjHeadData.Password_Expire_Date);
        this.mobjUserModel = pobjHeadData;
        this.msImgURL = CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.USER_Image) ? "../../../../assets/images/user.png" : this.mobjUserModel.USER_Image;
        this.marrCompanyList = pobjGetCopyData.DetailData;
        this.msCopyRecord = "Y";

        localStorage.removeItem('copyUserData');
      } else {
        this.bmlfGetCompanyList();

      }
      setTimeout(() => {
        document.getElementById("txtUserKey").focus();
      }, 200);
    } else {
      this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
      /**----customization----- */

      this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
      this.msUserKey = psUserKey;
      this.bmlfGetUserData();
    }
  }
  bmlfGetGroupType() {
    let pobjReqData = {
      ScreenName: "User Setup",
      LookUpKey: 'GroupType',
      Filter1: this.mobjUserModel.Group_ID,
      Filter2: this.msTenenatId
    }
    this.userFieldService.bmlfAdminGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        let pobjData = res.LookupData[0];
        this.msGroupType = pobjData.Group_Type;
      }
    });
  }

  bmlfGetCompanyList() {

    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "TenantID": this.msTenenatId
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetCompanyList(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.HeaderData.length > 0) {
          this.marrCompanyList = res.HeaderData;
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfGetUserData() {
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "User_Id": this.msUserKey,
      "TenantID": this.msTenenatId
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetUserSetup(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.HeaderData.length > 0) {
          let pobjUserData = res.HeaderData[0];
          this.msStartUserId = pobjUserData.User_ID.substring(0, 2);
          pobjUserData.User_ID = pobjUserData.User_ID.substring(2);
          pobjUserData.UserDOB = new Date(pobjUserData.UserDOB);
          pobjUserData.Password_expired_option = pobjUserData.Password_expired_option == 0 ? true : false;
          pobjUserData.Password_Expire_Date = pobjUserData.Password_Expire_Date == null ? null : new Date(pobjUserData.Password_Expire_Date);
          this.mobjUserModel = Object.assign({}, pobjUserData);
          this.mobjUserModel.User_Password = "123456789012345";
          this.msImgURL = CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.USER_Image) ? "../../../../assets/images/user.png" : this.mobjUserModel.USER_Image; // "data:image/png;base64,"+ this.mobjUserModel.eSignature;
          this.marrCompanyList = res.DetailData;
          if (this.msCopyRecord == "Y") {
            this.mobjUserModel.User_ID = "";
            this.mobjUserModel.User_Password = "";
            if (this.msAdminType == "Super Admin") {
              this.msTenenatId = localStorage.getItem("TenantIdWise");
            }
            //  this.msStartUserId = this.msTenenatId; by NG 171509
            document.getElementById("txtUserKey").focus();
          }else{
            this.bmlfGetGroupType();
          }
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfOnFormFieldChange() {
    this.mbIsChangeData = true;
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }
  bmlfonFileChanged(event) {
    if (event.target.files.length == 0) {
      return;
    }
    const file = event.target.files[0];
    let mimeType = file.type;
    if (mimeType.match(/image\/*/) == null) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.INVALIDEXTENSION");
      return;
    }
    if (file.size / 1024 / 1024 > 2) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.FILESIZEEXCEED");
      return;
    }
    const reader = new FileReader();

    reader.readAsDataURL(file);
    reader.onload = (_event) => {
      this.msImgURL = reader.result;
      this.mbIsChangeData = true;
    }
  }

  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {

    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psTitle != "") {
      this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    } else {
      this.mobjLookupKeyData.title = psTitle;
    }
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.mbIsLoading = false;
      this.msLookupField = '';
      return false;
    }
    let psFilter1 = this.msStartUserId;
    let psFilter2 = "";
    if (psField == "AdditionalGroups") {
      psFilter2 = this.mobjUserModel.Group_ID;
    }
    // let psReqData = {
    //   ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: psFilter1, Filter2: psFilter2, Filter3: "", Filter4: '', Filter5: ''
    // };
    if (this.msAdminType == "Super Admin") {
      if (psField == "Group_ID") {
        psLookUpKey = "PrimaryGroupSupAdm"
      } else if (psField == "AdditionalGroups") {
        psLookUpKey = "AdditionalGroupSupAdm"
      }
    }
    /* ------- Add New Super Admin Condtion ------------------------*/
    // As per discussion with Suraj sir 
    let psReqData;
    // if User is Super Admin
    if (psField == 'UserSetupListWithoutTenant') {
      psReqData = {
        ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: "", Filter2: "", Filter3: "", Filter4: '', Filter5: ''
      };
    }
    // if User is Tenant user
    else if (psField == 'UserSetupList') {
      psReqData = {
        ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: psFilter1, Filter2: psFilter2, Filter3: "", Filter4: '', Filter5: ''
      };
    }

    else {
      psReqData = {
        ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: psFilter1, Filter2: psFilter2, Filter3: "", Filter4: '', Filter5: ''
      };
    }
    /* ------- Add New Super Admin Condtion ------------------------*/


    if (psField == "TenantID") {
      psReqData = {
        ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: "", Filter2: psFilter2, Filter3: "", Filter4: '', Filter5: ''
      };
    }

    if (this.msAdminType == "Super Admin") {
      if (psField != "TenantID") {
        if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.TenantID)) {
          this.bmlfOpenErrorBlock("USERSETUP.MSG.SELECTTENANT");
          this.mbIsLoading = false;
          return false;
        }
      }
    }

    this.userFieldService.bmlfAdminLookupData(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '' && ele[key] != null) {
                ele[key] = ele[key] ? moment(ele[key]).format(this.msCustomDateFormat.toUpperCase()) : '';
              }
              // else {
              //   ele[key] = moment().format(this.msCustomDateFormat.toUpperCase() && this.msCustomDateFormat.toUpperCase());
              // }
            }
          });

          // if (psField == "Group_ID" && this.msGroupType == "") {
          //   if (ele.Group_ID == this.mobjUserModel.Group_ID) {
          //     this.msGroupType = ele.Group_Type;
          //   }
          // }
        })
        //scr 163908
        // As per amit sir
        // res.LookupData.forEach((ele) => {
        //   Object.keys(ele).forEach((key) => {
        //     let psKey = key.toUpperCase();
        //     if (psKey.search('DATE') != -1) {
        //       if (this.msCustomDateFormat!=undefined){ //scr 163908
        //         if (ele[key] && ele[key] != '') {
        //           ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
        //         } else {
        //           ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
        //         }
        //       }
        //     }
        //   });
        // })
        if (psField == "AdditionalGroups") {
          this.mbMutiSelectionLookup = true;
          this.mbIsQuantityLookup = true;
          let parrSplitUser = this.mobjUserModel.AdditionalGroups.split(",");
          if (parrSplitUser.length > 0) {
            res.LookupData.forEach(element => {
              let parrFilterData = parrSplitUser.filter(x => x == element.Group_ID);
              if (parrFilterData.length > 0) {
                element.checked = true;
              }
            });
          }
        }
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }

  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {

    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    this.mbIsChangeData = true;
    if (pobjEvent.type == 'custom') {
      if (pobjEvent.field == "UserSetupList") {
        this.bmlfOpenSelectedRecord(pobjEvent.value);
      } else if (pobjEvent.field == "UserSetupListWithoutTenant") {
        localStorage.setItem("TenantIdSearchLookup", pobjEvent.value.TenantID);
        this.bmlfOpenSelectedRecord(pobjEvent.value);
      } else if (pobjEvent.field == "TenantID") {
        //this.msTenenatId =pobjEvent.valueKey;
        //localStorage.setItem("TenantIdWise",pobjEvent.valueKey);
        //if (this.msStartUserId=="" || this.msStartUserId==null){
        this.msStartUserId = pobjEvent.valueKey;
        this.msTenenatId = pobjEvent.valueKey;
        this.mobjUserModel.Group_ID = "";
        this.mobjUserModel.AdditionalGroups = "";
        this.mobjUserModel.SignGroup_ID = "";
        this.bmlfGetCompanyList();
        setTimeout(function () { document.getElementById('txtUserKey').focus(); }, 10);
        //}
        //  this.mbIsChangeData = false;
      } else if (pobjEvent.field == "AdditionalGroups") {
        this.mbMutiSelectionLookup = false;
        this.mbIsQuantityLookup = false;
        this.mobjUserModel.AdditionalGroups = pobjEvent.value[0].Group_ID;
        if (pobjEvent.value.length > 1) {
          for (let i = 1; i < pobjEvent.value.length; i++) {
            this.mobjUserModel.AdditionalGroups += "," + pobjEvent.value[i].Group_ID
          }
        }
      } else {
        this.mobjUserModel[pobjEvent.field] = pobjEvent.valueKey;
        if (pobjEvent.field == "Group_ID") {
          if (this.msFormMode == "I") {
            this.bmlfGetUserCount();
          } else {
            if ((this.msGroupType != "WMS" && pobjEvent.value["Group_Type"] == "WMS") || (this.msGroupType == "WMS" && pobjEvent.value["Group_Type"] != "WMS")) {
              this.bmlfGetUserCount();
            }
          }
          // if (this.msFormMode == "I") {
          this.mobjUserModel.AdditionalGroups = "";
          //}
        }
      }
    }
  }
  bmlfGetInputColumn(psfield) {
    return 'Group_ID'
  }
  /**---------------Close lookup field------------- */
  bmlfCloseLookupModal(pobjEvent): void {
    this.mbMutiSelectionLookup = false;
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    if (this.msAdminType == "Super Admin") {
      if (pobjEvent.field == "UserSetupListWithoutTenant") {
        if (this.msFormMode == "U") {
          this.bmlfGetUserData();
        }
        return;
      }
    } else {
      if (pobjEvent.field == "UserSetupList") {
        if (this.msFormMode == "U") {
          this.bmlfGetUserData();
        }
        return;
      }
    }

    if (pobjEvent.eleId != null && pobjEvent.field != "AdditionalGroups") {
      let psInputKey = this.bmlfGetInputColumn(pobjEvent.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjEvent.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjEvent.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        if (document.getElementById(pobjEvent.eleId) != undefined) {
          document.getElementById(pobjEvent.eleId)['value'] = "";
        }

        if (this.mobjUserModel[pobjEvent.field] != undefined) {
          this.mobjUserModel[pobjEvent.field] = '';
          this.mobjUserModel[pobjEvent.field + "Desc"] = '';
          setTimeout(function () { document.getElementById(pobjEvent.eleId).focus(); }, 10);
        }
      }
    }
    let elementExists = document.getElementById(pobjEvent.eleId);
    if (elementExists) {
      document.getElementById(pobjEvent.eleId).focus();
    }
  }
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }
  bmlfGetUserCount() {
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: 'UserCount',
      Filter1: this.mobjUserModel.Group_ID,
      Filter2: this.mobjUserModel.User_ID,
      Filter3: this.msTenenatId
    }
    this.userFieldService.bmlfAdminGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        let parrUserCount = res.LookupData[0];
        if (parrUserCount.Group_Type == "Normal") {
          let pnumCount = parrUserCount.BMEUsers - parrUserCount.UsedBMEUsers
          if (pnumCount == 0) {
            this.mobjUserModel.Group_ID = "";
            this.bmlfOpenErrorBlock("USERSETUP.MSG.NORMALUSERLIMITEXCEED");
          }
        } else if (parrUserCount.Group_Type == "WMS") {
          let pnumCount = parrUserCount.WMSUsers - parrUserCount.UsedWMSUsers
          if (pnumCount == 0) {
            this.mobjUserModel.Group_ID = "";
            this.bmlfOpenErrorBlock("USERSETUP.MSG.WMSUSERLIMITEXCEED");
          }
        }
      }
    });
  }
  bmlfSelectAll(value) {
    this.marrCompanyList.forEach(element => {
      element.Select = value;
    });
  }
  bmlfChangePasswordExpire() {
    if (this.mobjUserModel.Password_expired_option) {
      this.mobjUserModel.Password_Expire_Date = null;
    } else {
      this.mobjUserModel.Password_Expire_Date = new Date();
    }
  }
  bmlfChangeUserId() {
    this.mbIsChangeData = true;
    if (this.msAdminType == "Super Admin") {
      if (CustomValidator.bmgfNullCheckValidator(this.msStartUserId)) {
        this.bmlfOpenErrorBlock("USERSETUP.MSG.SELECTTENANT");
        return false;
      }
    }

    if (CustomValidator.bmgfAlphaNumericValidator(this.mobjUserModel.User_ID)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.ALLOWONLYALPHANUMERIC");
      this.mobjUserModel.User_ID = "";
    }
  }
  bmlfValidData() {
    if (this.msAdminType == "Super Admin") {
      if (CustomValidator.bmgfNullCheckValidator(this.msStartUserId)) {
        this.bmlfOpenErrorBlock("USERSETUP.MSG.SELECTTENANT");
        return false;
      }
    }
    if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.User_ID)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.USERKEYREQ");
      return false;
    } else if (CustomValidator.bmgfAlphaNumericValidator(this.mobjUserModel.User_ID)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.ALLOWONLYALPHANUMERIC");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.MobileNo)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.MOBILENOREQ");
      return false;
      // } else if (CustomValidator.bmgfMobileNoValidator(this.mobjUserModel.MobileNo)) {
      //   this.bmlfOpenErrorBlock("USERSETUP.MSG.INVALIDMOBILENO");
      //   return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.UserDOB)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.BIRTHDATEREQ");
      return false;
      // } else if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.User_Password)) {
      //   this.bmlfOpenErrorBlock("USERSETUP.MSG.PASSWORDREQ");
      //   return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.Group_ID)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.GROUPIDREQ");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.user_Email)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.EMAILREQ");
      return false;
    } else if (CustomValidator.bmgfEmailValidator(this.mobjUserModel.user_Email)) {
      this.bmlfOpenErrorBlock("USERSETUP.MSG.INVALIDEMAIL");
      return false;
    } else {
      return true;
    }
  }
  bmlfSubmitData(pbClose: boolean = false): void {
    // if (!this.utilService.mbWrite) {
    //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
    //   return;
    // };
    if (this.mbIsChangeData == false && this.msFormMode == 'U') {
      return;
    }
    if (!this.bmlfValidData()) {
      this.mbIsSearchMode = false;
      this.mbIsAddMode = false;
      this.mbIsCopyMode = false;
      return;
    }

    this.bmlfSaveUserData();
  }
  bmlfSaveUserData(): void {
    this.mbIsLoading = true;
    let parrHeaderData = [];
    let pobjUserData: any = {};
    let parrDetailData = [];
    pobjUserData = Object.assign({}, this.mobjUserModel);
    pobjUserData.User_ID = this.msStartUserId + pobjUserData.User_ID;
    pobjUserData.TenantID = this.msStartUserId;
    pobjUserData.User_Password = "";
    pobjUserData.UserDOB = moment(pobjUserData.UserDOB).format(this.msServeDateFormat);
    pobjUserData.USER_Image = this.msImgURL == "../../../../assets/images/user.png" ? "" : this.msImgURL;
    pobjUserData.Password_expired_option = pobjUserData.Password_expired_option ? 0 : 1;
    pobjUserData.Password_Expire_Date = pobjUserData.Password_Expire_Date != null ? moment(pobjUserData.Password_Expire_Date).format(this.msServeDateFormat) : null;
    parrHeaderData.push(pobjUserData);

    let parrFilterData = this.marrCompanyList.filter(x => x.Select == true);
    if (parrFilterData.length > 0) {
      parrDetailData = parrFilterData;
      // } else {
      //   if (this.marrCompanyList.length > 0) {
      //     parrDetailData.push(this.marrCompanyList[0]);
      //   }
    }
    let pobjJData = {
      HeaderData: parrHeaderData,
      DetailData: parrDetailData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveUserSetup(pobjJson, this.msFormMode).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mbIsChangeData = false;
          if (this.msFormMode == "I") {

            this.msFormMode = "U";
            this.msCopyRecord = "N";
            this.msUserKey = this.msStartUserId + this.mobjUserModel.User_ID;
            this.bmlfResetPassword("I");
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          } else if (this.msFormMode == "U") {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
          }

          if (this.mbIsSearchMode) {
            this.mbIsSearchMode = false;
            if (this.msAdminType == "Super Admin") {

              this.bmlfOpenLookupModal('UserSetupListWithoutTenant', this.msScreenName, 'UserSetupListWithoutTenant', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TENANTID');
            } else {
              this.bmlfOpenLookupModal('UserSetupList', this.msScreenName, 'UserSetupList', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TITLE');
            }
          } else if (this.mbIsAddMode) {
            this.mbIsAddMode = false;
            this.bmlfRefreshForm();
          } else if (this.mbIsCopyMode) {
            this.mbIsCopyMode = false;
            this.bmlfCopyData();
          } else if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            if (this.msAdminType == "Super Admin") {
              localStorage.removeItem('TenantIdSearchLookup');
              localStorage.removeItem('TenantIdWise');
              this.router.navigate(["/main/admin/userlist"]);
            } else {
              this.router.navigate(["/main/admin/user"]);
            }
          } else {
            this.bmlfGetUserData();
          }
        } else {
          this.mbIsSearchMode = false;
          this.mbIsAddMode = false;
          this.mbIsCopyMode = false;
          this.mbIsDeleteMode = false;
          this.mbIsCloseMode = false;
          this.toastMessageService.notifyError(res.Response[0].ResponseText)
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfCloseUserSetUp(): void {
    if (this.mbIsChangeData == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      if (this.msAdminType == "Super Admin") {
        localStorage.removeItem('TenantIdSearchLookup');
        localStorage.removeItem('TenantIdWise');
        this.router.navigate(["/main/admin/userlist"]);
      } else {
        this.router.navigate(["/main/admin/user"]);
      }
    }
  }
  // So Footer Action finction
  bmlfFooterActionFn(event) {
    if (event == 'searchEvn') {
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsSearchMode = true
        return false;
      }
      if (this.msAdminType == "Super Admin") {
        this.bmlfOpenLookupModal('UserSetupListWithoutTenant', this.msScreenName, 'UserSetupListWithoutTenant', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TITLE');
      } else {
        this.bmlfOpenLookupModal('UserSetupList', this.msScreenName, 'UserSetupList', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TITLE');
      }
    } else if (event == 'addEvn') {
      // if (!this.utilService.mbWrite) {
      //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      //   return false;
      // };
      if (!this.bmlfValidData()) {
        return;
      }
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsAddMode = true //for Super Admin
        return false;
      }
      this.bmlfRefreshForm();
    } else if (event == 'deleteEvn') {
      // if (!this.utilService.mbDelete) {
      //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      //   return false;
      // };
      if (this.mobjUserModel.User_ID == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbIsChangeData) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
        return false;
      }
      if (this.mobjUserModel.User_ID == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbIsChangeData) {
        this.bmlfRefreshForm();
        return false;
      }
      this.mbShowDeleteDialog = true;
    } else if (event == 'copyEvn') {
      // if (!this.utilService.mbWrite) {
      //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      //   return false;
      // };
      if (CustomValidator.bmgfNullCheckValidator(this.mobjUserModel.User_ID)) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate("COMMON.MSG.COPY"));
        document.getElementById("txtUserKey").focus();
        return;
      }
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsCopyMode = true;
        return false;
      }
      this.bmlfCopyData();

    }
  }
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsSearchMode) {
        this.mbIsSearchMode = false;
        if (this.msAdminType == "Super Admin") {
          this.bmlfOpenLookupModal('UserSetupListWithoutTenant', this.msScreenName, 'UserSetupListWithoutTenant', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TENANTID');
        } else {
          this.bmlfOpenLookupModal('UserSetupList', this.msScreenName, 'UserSetupList', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TITLE');
        }
      } else if (this.mbIsAddMode) {
        this.mbIsAddMode = false;
        this.bmlfRefreshForm();
      } else if (this.mbIsCopyMode) {
        this.mbIsCopyMode = false;
        this.bmlfCopyData();
      } else if (this.mbIsDeleteMode) {
        this.mbIsDeleteMode = false;
        this.mbShowDeleteDialog = true;
      }
      else {
        if (this.msAdminType == "Super Admin") {
          localStorage.removeItem('TenantIdSearchLookup');
          localStorage.removeItem('TenantIdWise');
          this.router.navigate(["/main/admin/userlist"]);
        } else {
          this.router.navigate(['/main/admin/user']);
        }
      }
    } else {
      this.mbSaveDialogView = false;
      this.mbIsSearchMode = false;
      this.mbIsCopyMode = false;
      this.mbIsAddMode = false;
      this.mbIsCloseMode = false;
    }
  }
  // Delete Sales Order Dialog events
  bmlfonDialogAction(psAction) {
    if (psAction == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (psAction == 'Yes') {
      if (this.msFormMode == "I") {
        this.bmlfRefreshForm();
        return false;
      }
      let psUserId = this.msStartUserId + this.mobjUserModel.User_ID;
      let parrHeaderData = [];
      parrHeaderData = [{ User_Id: psUserId, TenantID: this.msStartUserId }]
      let reqData: any = {
        JSON: JSON.stringify({
          ParaData: parrHeaderData,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteUser(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
            this.mbShowDeleteDialog = false;
            this.bmlfRefreshForm();
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }
  bmlfOpenSelectedRecord(data) {
    let param = { userId: data.User_ID };
    if (this.msAdminType == "Super Admin") {
      this.utilService.bmgfRefreshReuse('/main/admin/userlist/manage', true, param);
    } else {
      this.utilService.bmgfRefreshReuse('/main/admin/user/manage', true, param);
    }
  }
  // Refresh Sales Order
  bmlfRefreshForm() {
    if (this.msAdminType == "Super Admin") {
      localStorage.setItem("TenantIdWise", this.msStartUserId);
      this.utilService.bmgfRefreshReuse('/main/admin/userlist/manage', false);
    } else {
      this.utilService.bmgfRefreshReuse('/main/admin/user/manage', false);
    }
  }
  // Copy Current WHT Data
  bmlfCopyData() {
    let HeaderData = Object.assign({}, this.mobjUserModel);
    HeaderData.User_ID = "";
    HeaderData.User_Password = "";
    // if(this.msAdminType=="Super Admin"){
    //   this.msStartUserId="";
    // }
    let parrCopyData = {
      HeaderData: HeaderData,
      DetailData: this.marrCompanyList,
    }
    localStorage.setItem('copyUserData', JSON.stringify(parrCopyData));
    this.bmlfRefreshForm();
  }
  bmlfResetPassword(type = null) {
    this.mbIsLoading = true;
    let pobjOData = { loginData: [], Response: [] };
    pobjOData.loginData.push({
      "loginId": this.msUserKey,
      "email": this.mobjUserModel.user_Email,
      "phoneDetails": this.mobjUserModel.MobileNo
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": "",
      "MailResponse": "",
      "SMSResponse": ""

    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfResetPassword(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.Response.length > 0) {
          if (res.Response[0].ErrorCode == "") {
            if (type == "I") {
              this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("USERSETUP.MSG.OTPMSG"));
            } else {
              this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("USERSETUP.MSG.RESETPASSWORDMSG"));
            }
          } else {
            this.toastMessageService.notifyError(res.Response[0].ResponseText);
          }
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.msStartUserId + this.mobjUserModel.User_ID;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/User Setup.pdf', '_blank');
  }
  bmlfTenant() {
    this.bmlfOpenLookupModal('TenantID', this.msScreenName, 'TenantID', 'click', '', 'custom', 'cmdSearch', 'USERSETUP.TENANTID');
  }



}
