import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminAuthGuard, AuthGuard, SuperAdminAuthGuard } from 'src/app/core/guards/auth.guard';
import { DataViewComponent } from 'src/app/shared/components/data-view/data-view.component';
import { DownloadErrorComponent } from 'src/app/shared/components/download-error/download-error.component';
import { AdminComponent } from './admin.component';
import { AdminLendingComponent } from './components/admin-lending/admin-lending.component';
import { UserGroupListComponent } from './components/user-group-setup/user-group-list/user-group-list.component';
import { UserGroupManageComponent } from './components/user-group-setup/user-group-manage/user-group-manage.component';
import { UserListComponent } from './components/user-setup/user-list/user-list.component';
import { UserManageComponent } from './components/user-setup/user-manage/user-manage.component';
import { CompanyInformationComponent } from './components/company-information/company-information/company-information.component';
import { CompanyInformationListComponent } from './components/company-information/company-information-list/company-information-list.component';
import { PasswordConfigurationComponent } from './components/password-configuration/password-configuration.component';
import { TenantSetupListComponent } from './components/tenant-setup/tenant-setup-list/tenant-setup-list.component';
import { TenantSetupManageComponent } from './components/tenant-setup/tenant-setup-manage/tenant-setup-manage.component';
import { UserGroupSetupReportComponent } from './components/user-group-setup-report/user-group-setup-report.component';
import { UserActivityLogComponent } from './components/user-activity-log/user-activity-log.component';
import { SequenceNumberSetupComponent } from './components/sequence-number-setup/sequence-number-setup.component';
import { DefaultSetupComponent } from './components/default-setup/default-setup.component';
import { CustomFieldsConfigurationComponent } from './components/custom-fields-configuration/custom-fields-configuration.component';
import {WindowsServiceConfigurationComponent} from './components/windows-service-configuration/windows-service-configuration.component';
import { UserValidationManageComponent } from './components/user-validation/user-validation-manage/user-validation-manage.component';
const routes: Routes = [
    {
        path: '',
        component: AdminComponent,
        children:
          [
            {
              path: '', redirectTo: 'admin', pathMatch: 'full'
            },
            {
              path: 'admin', component: AdminLendingComponent
            },
            {
              path: 'user-group', component: UserGroupListComponent, canActivate: [AdminAuthGuard]
            },
            {
              path: 'user-group/manage', component: UserGroupManageComponent, canActivate: [AdminAuthGuard]
            },
             {
               path: 'user', component: UserListComponent, canActivate: [AdminAuthGuard]
             },
            {
              path: 'user/manage', component: UserManageComponent, canActivate: [AdminAuthGuard]
            },
            {
              path: 'data-view', component: DataViewComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'download-error', component: DownloadErrorComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'company-information-list', component: CompanyInformationListComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'company-information', component: CompanyInformationComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'userlist', component: UserListComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'userlist/manage', component: UserManageComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'user-group-list', component: UserGroupListComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'user-group-list/manage', component: UserGroupManageComponent, canActivate: [SuperAdminAuthGuard]
            }
            ,
            {
              path: 'password-configuration', component: PasswordConfigurationComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'tenant-setup-list', component: TenantSetupListComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'tenant-setup-list/manage', component: TenantSetupManageComponent, canActivate: [SuperAdminAuthGuard]
            }, {
              path: 'user-group-setup-report', component: UserGroupSetupReportComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'user-activity-log', component: UserActivityLogComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'sequence-number-setup', component: SequenceNumberSetupComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'default-setup', component: DefaultSetupComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path: 'custom-fields-configuration', component: CustomFieldsConfigurationComponent, canActivate: [SuperAdminAuthGuard]
            },
            {
              path:'windows-service-configuration', component:WindowsServiceConfigurationComponent, canActivate:[SuperAdminAuthGuard]
            },
            {
              path: 'user-validation', component:UserValidationManageComponent,canActivate: [SuperAdminAuthGuard]
            },
          ]
      }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AdminRoutingModule { }




