import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
})
export class UserSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteUserGroup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteUserGroup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetUserGroup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetUserGroup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetSMenuGroup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSmenuGroup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveUserGroupSetup(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFCreateUserGroup`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdateUserGroup`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }
    public bmlfDeleteUser(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteUserSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetUserSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUserSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetCompanyList(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetCompanyList`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfResetPassword(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/Login/ResetPassword`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveUserSetup(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateUserSetup`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateUserSetup`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }
}