import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { UserSetupService } from '../../../http/user-setup.service';
import * as moment from 'moment';
import { UserGroupSetup } from '../../../constant/user-group-setup.const';

@Component({
  selector: 'app-user-group-manage',
  templateUrl: './user-group-manage.component.html',
  styleUrls: ['./user-group-manage.component.scss']
})
export class UserGroupManageComponent extends BaseGrid implements OnInit {
  public marrModuleNameCol: any = UserGroupSetup.garrModuleColumn.map(ele => Object.assign({}, ele));
  public marrDetailCol: any = UserGroupSetup.garrModuleDetailColumn.map(ele => Object.assign({}, ele));
  public getDataList(): void { }
  public gridDataList: any;
  public mbIsLoading: boolean = false;
  public msGrpId: string;
  public msGrpType: string = "Normal";
  public msScreenName: string = 'User Group Setup';
  public mbIsChangeData: boolean = false;
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public autoCorrect: boolean = true;
  public msFormMode: string = "I";
  public msCloseActionModalName: string = "";
  public mbSaveDialogView: boolean = false;
  public mbIsSearchMode: boolean = false;
  public mbIsAddMode: boolean = false;
  public mbIsCopyMode: boolean = false;
  public mbIsDeleteMode: boolean = false;
  public mbIsCloseMode: boolean = false;
  public msCopyRecord: string = "N";
  public mnumDeleteLineIndex: number;
  public mbDeleteLineItemDialog: boolean = false;
  public mbShowDeleteDialog: boolean = false;
  /**---Lookup realted field--- */
  public mobjUserData: any = {};
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModalTitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  /**---END : Lookup realted field--- */
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/
  public marrModuleList: any = [];
  public marrOriginalModuleList: any = [];

  public marrDetailData: any = [];
  //public mobjUserGroupModel: any = { Group_ID: "", Group_Description: "", Group_Type: "Normal", ALLOWCHANGESCREENLAYOUT: false };
  public mobjUserGroupModel: any = { Group_ID: "", Group_Description: "", Group_Type: "Normal", AllowChangeLayout: false };

  public marrGroupTypeList: any = []; //[{ key: "Normal" }, { key: "WMS" },{key:"Sampling"}];
  // As per amit sir
  //public marrGroupTypeListSuperAdmin: any = [{ key: "Normal" }, { key: "WMS" },{ key: "Admin" },{ key: "Super Admin" }];
  // public marrGroupTypeListSuperAdmin: any = [{ key: "Normal" }, { key: "WMS" },{ key: "Tenant Admin" },{ key: "Admin" },{key:"Sampling"}];
  public mobjPermission: any = { DeletePermission: false, NonePermission: false, WritePermission: false, ReadPermission: false };
  public TennatId: string = "01";
  public msAdminType: string = '';
  public msTenantIdSearch: string = '';
  constructor(private route: ActivatedRoute,
    private router: Router,
    private formService: UserSetupService,
    public toastMessageService: ToastMessageService,
    public dateFormatService: DateFormatService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService, private lsService: LocalStorageService) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
  }

  ngOnInit(): void {

    /*--------- get screen permission --------*/
    // this.utilService.getBMEScreePermission(this.msScreenName);
    this.msAdminType = localStorage.getItem("isLoginType")
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.bmlfGetGroupTypeList();
    if (this.msAdminType == "Super Admin") {
      // this.marrGroupTypeList=this.marrGroupTypeListSuperAdmin;
      if (this.route.snapshot.queryParamMap.get('TenantID') == null) { //search
        this.TennatId = localStorage.getItem("TenantIdSearchLookupforGroup");
      } else { //list page      
        this.TennatId = this.route.snapshot.queryParamMap.get('TenantID') != null ? this.route.snapshot.queryParamMap.get('TenantID') : '';
      }
    } else {
      this.TennatId = this.mobjUserData.TenantId != null ? this.mobjUserData.TenantId : '';
    }
    let psGrpKey = this.route.snapshot.queryParamMap.get('grpId');
    let psGrpType = this.route.snapshot.queryParamMap.get('grpTyp');
    let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');

    if (CustomValidator.bmgfNullCheckValidator(psGrpKey)) {
      this.msFormMode = "I";
      this.msGrpId = "";

      if (this.msAdminType == "Super Admin") {
        this.TennatId = localStorage.getItem("TenantIdWiseforGroup") != null ? localStorage.getItem("TenantIdWiseforGroup") : '';
      }

      let pobjCopyData = localStorage.getItem('copyUserGroupData');
      if (pobjCopyData) {
        let pobjGetCopyData: any = JSON.parse(pobjCopyData);
        this.mobjUserGroupModel = Object.assign({}, pobjGetCopyData.HeaderData);
        this.marrModuleList = pobjGetCopyData.DetailData;
        this.msCopyRecord = "Y";
        localStorage.removeItem('copyUserGroupData');
      } else {
        this.bmlfGetSMenuData();
      }
      setTimeout(() => {
        document.getElementById("txtGrpKey").focus();
      }, 200);
    } else {
      this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
      /**----customization----- */
      this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
      this.msGrpId = psGrpKey;
      this.msGrpType = psGrpType;
      this.mobjUserGroupModel.Group_Type = psGrpType;
      this.bmlfGetSMenuData();
    }
  }
  bmlfGetGroupTypeList(): void {
    this.marrGroupTypeList = [];
    let pobjReqData = {
      ScreenName: "UserGroupSetup",
      LookUpKey: this.msAdminType == "Super Admin" ? "GroupType" : "TenantGroupType",
      Filter1: 'Group Type'
    };
    this.userFieldService.bmlfAdminGeneralSqlData(pobjReqData).subscribe(
      res => {
        if (res.Response[0].ErrorCode == "") {
          if (res.LookupData.length > 0) {
            this.marrGroupTypeList = res.LookupData;
            if (this.msCopyRecord != "Y") {
              this.mobjUserGroupModel.Group_Type = "Normal";
            }
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      }
    ).add(() => { })
  }
  bmlfGetSMenuData(onChange = false) {
    this.marrOriginalModuleList = [];
    this.marrModuleList = [];
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "Group_Id": this.msGrpId,
      "CompanyID": "", //this.TennatId
      "GroupType": this.msGrpType
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetSMenuGroup(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.length > 0) {
          this.marrOriginalModuleList = res;
          if ((this.msFormMode == "U" || (this.msCopyRecord == "Y" && this.msFormMode == "I")) && onChange == false) {
            this.bmlfGetUserGroupData();
          } else {
            this.marrModuleList = this.marrOriginalModuleList;
          }
        } else {
          if (this.msGrpType == "Tenant Admin" || this.msGrpType == "Admin")
            this.bmlfGetUserGroupData();
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfGetUserGroupData() {
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "Group_Id": this.msGrpId,
      "CompanyID": this.TennatId
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetUserGroup(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.HeaderData.length > 0) {
          this.mobjUserGroupModel = Object.assign({}, res.HeaderData[0]);
          this.marrDetailData = res.DetailData;
          this.marrOriginalModuleList.forEach(element => {
            element.ScreenNameData.forEach(ele => {
              let marrFilterData = this.marrDetailData.filter(x => x.menuitem_id == ele.MenuItem_ID);
              if (marrFilterData.length > 0) {
                ele.ReadPermission = marrFilterData[0].ReadPermission;
                ele.WritePermission = marrFilterData[0].WritePermission;
                ele.NonePermission = marrFilterData[0].NonePermission;
                ele.DeletePermission = marrFilterData[0].DeletePermission;
                ele.ScreenField = marrFilterData[0].ScreenField;
              }
            });
          });
          this.marrModuleList = this.marrOriginalModuleList;
          if (this.msCopyRecord == "Y") {
            this.mobjUserGroupModel.Group_ID = "";
            // if(this.msAdminType=="Super Admin"){
            //   this.TennatId =localStorage.getItem("TenantIdWise");
            // }
            document.getElementById("txtGrpKey").focus();
          }
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfChangeGroupType() {
    this.msGrpType = this.mobjUserGroupModel.Group_Type;
    this.mobjUserGroupModel.AllowChangeLayout = false;
    this.bmlfGetSMenuData(true);
  }
  bmlfOnFormFieldChange() {
    this.mbIsChangeData = true;
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }
  /**--------Start: Lookup Functionality----- */
  // LOOKUP MODAL 
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {

    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjUserGroupModel[psField] = "";
      this.mbIsLoading = false;
      return false
    }
    let psFilter1 = "";
    if (psField == "UserGroupList") {
      psFilter1 = this.TennatId;
    }
    if (psField == "AssignedUsers") {
      psFilter1 = this.mobjUserGroupModel.Group_ID;
    }

    let psReqData = {
      ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: psFilter1, Filter2: "", Filter3: "", Filter4: '', Filter5: ''
    };

    this.userFieldService.bmlfAdminLookupData(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }

      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          });
        })
        //scr 163908
        // As per amit sir
        // res.LookupData.forEach((ele) => {
        //   Object.keys(ele).forEach((key) => {
        //     let psKey = key.toUpperCase();
        //     if (psKey.search('DATE') != -1) {
        //       if (this.msCustomDateFormat!=undefined){ 
        //         if (ele[key] && ele[key] != '') {
        //           ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
        //         } else {
        //           ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
        //         }
        //       }
        //     }
        //   });
        // })
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }

  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {

    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    if (pobjEvent.type == 'custom') {
      if (pobjEvent.field == "UserGroupList") {
        this.bmlfOpenSelectedRecord(pobjEvent.value);
      }
      if (pobjEvent.field == "UserGroupSetupListWithoutTenant") {
        localStorage.setItem("TenantIdSearchLookupforGroup", pobjEvent.value.TenantID);
        if (pobjEvent.value.Group_Type.toUpperCase() == 'ADMIN') { //scr 163973
          this.mobjUserGroupModel.TennatId = pobjEvent.value.TenantID;
          this.TennatId = pobjEvent.value.TenantID;
          this.mobjUserGroupModel.Group_ID = pobjEvent.value.Group_ID;
          this.mobjUserGroupModel.Group_Description = pobjEvent.value.Group_Description;
          this.mobjUserGroupModel.Group_Type = pobjEvent.value.Group_Type;
          if (pobjEvent.value.Group_Type == "ADMIN") {
            this.mobjUserGroupModel.Group_Type = "Admin";
          }
          this.marrModuleList = [];
        } else if (pobjEvent.value.Group_Type == "Tenant Admin") {
          this.mobjUserGroupModel.TennatId = pobjEvent.value.TenantID;
          this.TennatId = pobjEvent.value.TenantID;
          this.mobjUserGroupModel.Group_ID = pobjEvent.value.Group_ID;
          this.mobjUserGroupModel.Group_Description = pobjEvent.value.Group_Description;
          this.mobjUserGroupModel.Group_Type = pobjEvent.value.Group_Type;
          this.bmlfGetSMenuData()
        } else {
          this.bmlfOpenSelectedRecord(pobjEvent.value);
        }

      }
      if (pobjEvent.field == "TenantID") {
        // localStorage.setItem("TenantIdWiseforGroup",pobjEvent.valueKey);
        //if (this.TennatId=="" || this.TennatId==null){
        this.TennatId = pobjEvent.valueKey
        //this.bmlfOpenSelectedRecord(pobjEvent.value);
        //}
        this.mbIsChangeData = false;
      }
    }
  }

  bmlfCloseLookupModal(pobjEvent): void {
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    document.getElementById(pobjEvent.eleId).focus();
  }
  /**--------End: Lookup Functionality----- */
  bmlfChangeAllPermission(type) {
    if (type == "NONE" && this.mobjPermission.NonePermission == true) {
      this.mobjPermission.ReadPermission = false;
      this.mobjPermission.WritePermission = false;
      this.mobjPermission.DeletePermission = false;
    } else if (type == "WRITE" && this.mobjPermission.WritePermission == true) {
      this.mobjPermission.ReadPermission = true;
      this.mobjPermission.NonePermission = false;
    } else if (type == "DELETE" && this.mobjPermission.DeletePermission == true) {
      this.mobjPermission.NonePermission = false;
    } else if (type == "READ" && this.mobjPermission.ReadPermission == true) {
      this.mobjPermission.NonePermission = false;
    } else if (type == "READ" && this.mobjPermission.ReadPermission == false) {
      if (this.mobjPermission.WritePermission == true) {
        setTimeout(() => {
          this.mobjPermission.ReadPermission = true;
        }, 100);
      }
      this.mobjPermission.NonePermission = false;
    }
  }
  bmlfApplyPermissionData() {
    this.mbIsChangeData = true;
    this.marrModuleList.forEach(ele => {
      ele.ReadPermission = this.mobjPermission.ReadPermission ? true : false;
      ele.WritePermission = this.mobjPermission.WritePermission ? true : false;
      ele.NonePermission = this.mobjPermission.NonePermission ? true : false;
      ele.DeletePermission = this.mobjPermission.DeletePermission ? true : false;
      ele.ScreenNameData.forEach(element => {
        element.ReadPermission = this.mobjPermission.ReadPermission ? true : false;
        element.WritePermission = this.mobjPermission.WritePermission ? true : false;
        element.NonePermission = this.mobjPermission.NonePermission ? true : false;
        element.DeletePermission = this.mobjPermission.DeletePermission ? true : false;
      })
    });

  }
  bmlfChangeModulePermission(piIndex, type) {
    this.mbIsChangeData = true;
    if (type == "NONE" && this.marrModuleList[piIndex].NonePermission == true) {
      this.marrModuleList[piIndex].ReadPermission = false;
      this.marrModuleList[piIndex].WritePermission = false;
      this.marrModuleList[piIndex].DeletePermission = false;
    } else if (type == "WRITE" && this.marrModuleList[piIndex].WritePermission == true) {
      this.marrModuleList[piIndex].ReadPermission = true;
      this.marrModuleList[piIndex].NonePermission = false;
    } else if (type == "DELETE" && this.marrModuleList[piIndex].DeletePermission == true) {
      this.marrModuleList[piIndex].NonePermission = false;
    } else if (type == "READ" && this.marrModuleList[piIndex].ReadPermission == true) {
      this.marrModuleList[piIndex].NonePermission = false;
    } else if (type == "READ" && this.marrModuleList[piIndex].ReadPermission == false) {
      if (this.marrModuleList[piIndex].WritePermission == true) {
        setTimeout(() => {
          this.marrModuleList[piIndex].ReadPermission = true;
        }, 100);
      }
      this.marrModuleList[piIndex].NonePermission = false;
    }
    setTimeout(() => {
      this.marrModuleList[piIndex].ScreenNameData.forEach(element => {
        element.ReadPermission = this.marrModuleList[piIndex].ReadPermission ? true : false;
        element.WritePermission = this.marrModuleList[piIndex].WritePermission ? true : false;
        element.NonePermission = this.marrModuleList[piIndex].NonePermission ? true : false;
        element.DeletePermission = this.marrModuleList[piIndex].DeletePermission ? true : false;
      })
    }, 200);
  }
  bmlfChangeScreenPermission(index, parrIndex, type) {
    this.mbIsChangeData = true;
    if (type == "NONE" && this.marrModuleList[parrIndex].ScreenNameData[index].NonePermission == true) {
      this.marrModuleList[parrIndex].ScreenNameData[index].ReadPermission = false;
      this.marrModuleList[parrIndex].ScreenNameData[index].WritePermission = false;
      this.marrModuleList[parrIndex].ScreenNameData[index].DeletePermission = false;
    } else if (type == "WRITE" && this.marrModuleList[parrIndex].ScreenNameData[index].WritePermission == true) {
      this.marrModuleList[parrIndex].ScreenNameData[index].ReadPermission = true;
      this.marrModuleList[parrIndex].ScreenNameData[index].NonePermission = false;
    } else if (type == "DELETE" && this.marrModuleList[parrIndex].ScreenNameData[index].DeletePermission == true) {
      this.marrModuleList[parrIndex].ScreenNameData[index].NonePermission = false;
    } else if (type == "READ" && this.marrModuleList[parrIndex].ScreenNameData[index].ReadPermission == true) {
      this.marrModuleList[parrIndex].ScreenNameData[index].NonePermission = false;
    } else if (type == "READ" && this.marrModuleList[parrIndex].ScreenNameData[index].ReadPermission == false) {
      if (this.marrModuleList[parrIndex].ScreenNameData[index].WritePermission == true) {
        setTimeout(() => {
          this.marrModuleList[parrIndex].ScreenNameData[index].ReadPermission = true;
        }, 100);
      }
      this.marrModuleList[parrIndex].ScreenNameData.NonePermission = false;
    }
  }
  bmlfPrepareDetailData() {
    let parrDetailData = [];
    this.marrModuleList.forEach(element => {
      element.ScreenNameData.forEach(ele => {
        let pobj = Object.assign({}, ele);
        let pobjTemp: any = {};
        pobjTemp.ReadPermission = pobj.ReadPermission;
        pobjTemp.WritePermission = pobj.WritePermission;
        pobjTemp.NonePermission = pobj.NonePermission;
        pobjTemp.DeletePermission = pobj.DeletePermission;
        pobjTemp.ScreenField = pobj.ScreenField != undefined ? pobj.ScreenField : false;
        pobjTemp.module_name = pobj.Module_Name;
        pobjTemp.group_id = this.mobjUserGroupModel.Group_ID;
        pobjTemp.menuitem_name = pobj.MenuItem_Name;
        pobjTemp.menuitem_id = pobj.MenuItem_ID;
        if (pobj.NonePermission == true) {
          pobjTemp.permission = "None";
          pobjTemp.Permission1 = "";
        } else if (pobj.ReadPermission == true && pobj.WritePermission == true && pobj.DeletePermission == true) {
          pobjTemp.permission = "Write";
          pobjTemp.Permission1 = "Delete";
        } else if (pobj.ReadPermission == true && pobj.WritePermission == true) {
          pobjTemp.permission = "Write";
          pobjTemp.Permission1 = "None";
        } else if (pobj.ReadPermission == true && pobj.DeletePermission == true) {
          pobjTemp.permission = "Read";
          pobjTemp.Permission1 = "Delete";
        } else if (pobj.ReadPermission == true) {
          pobjTemp.permission = "Read";
          pobjTemp.Permission1 = "None";
        } else if (pobj.DeletePermission == true) {
          pobjTemp.permission = "None";
          pobjTemp.Permission1 = "Delete";
        } else {
          pobjTemp.permission = "None";
          pobjTemp.Permission1 = "None";
        }
        parrDetailData.push(pobjTemp);
      });
    });
    return parrDetailData;
  }
  bmlfChangeGroupId() {
    if (this.msAdminType == "Super Admin") {
      if (this.mobjUserGroupModel.Group_Type != "Admin" && CustomValidator.bmgfNullCheckValidator(this.TennatId)) {
        this.bmlfOpenErrorBlock("USERGROUPSETUP.MSG.SELECTTENANT");
        return false;
      }
    }

    if (CustomValidator.bmgfAlphaNumericValidator(this.mobjUserGroupModel.Group_ID)) {
      this.bmlfOpenErrorBlock("USERGROUPSETUP.MSG.ALLOWONLYALPHANUMERIC");
      this.mobjUserGroupModel.Group_ID = "";
    }
  }
  bmlfValidData() {


    if (this.msAdminType == "Super Admin") {
      if (this.mobjUserGroupModel.Group_Type != "Admin" && CustomValidator.bmgfNullCheckValidator(this.TennatId)) {
        this.bmlfOpenErrorBlock("USERGROUPSETUP.MSG.SELECTTENANT");
        return false;
      }
    }

    if (CustomValidator.bmgfNullCheckValidator(this.mobjUserGroupModel.Group_ID)) {
      this.bmlfOpenErrorBlock("USERGROUPSETUP.MSG.GROUPKEYREQ");
      return false;
    } else if (CustomValidator.bmgfAlphaNumericValidator(this.mobjUserGroupModel.Group_ID)) {
      this.bmlfOpenErrorBlock("USERGROUPSETUP.MSG.ALLOWONLYALPHANUMERIC");
      return false;
    } else {
      return true;
    }
  }
  bmlfSubmitData(pbClose: boolean = false): void {
    // if (!this.utilService.mbWrite) {
    //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
    //   return;
    // };
    if (this.mbIsChangeData == false && (this.msFormMode == 'U')) {
      return;
    }
    if (!this.bmlfValidData()) {
      this.mbIsSearchMode = false;
      this.mbIsCopyMode = false;
      this.mbIsAddMode = false;
      return;
    }

    // if (this.mbIsEsignature) {
    //     this.bmlfopenESignatureModal();
    //     return;
    // }
    this.bmlfSaveUserGroupData();
  }
  bmlfSaveUserGroupData(): void {

    this.mbIsLoading = true;
    let parrHeaderData = [];
    let parrDetailData = [];
    parrHeaderData.push(Object.assign({}, this.mobjUserGroupModel));
    if (this.msFormMode == "U") {
      parrHeaderData[0].TenantID = this.TennatId || "";
    }

    if (this.msAdminType == "Super Admin") {
      parrHeaderData[0].TenantID = this.TennatId || "";

      //scr 162764
      // as per amit sir
      // if(this.mobjUserGroupModel.Group_Type=="Super Admin"){
      //   parrHeaderData[0].Group_Type = "Admin";
      // }
      // if(this.mobjUserGroupModel.Group_Type=="Admin"){
      //   parrHeaderData[0].Group_Type = "Tenant Admin";
      // }

    }

    parrHeaderData[0].CompanyID = this.TennatId || "";
    parrDetailData = this.bmlfPrepareDetailData();
    let pobjJData = {
      HeaderData: parrHeaderData,
      DetailData: parrDetailData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveUserGroupSetup(pobjJson, this.msFormMode).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mbIsChangeData = false;
          if (this.msFormMode == "I") {
            this.msFormMode = "U";
            this.msCopyRecord = "N";
            this.msGrpId = this.mobjUserGroupModel.Group_ID;
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          } else if (this.msFormMode == "U") {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
          }
          if (this.mbIsSearchMode) {
            this.mbIsSearchMode = false;
            if (this.msAdminType == "Super Admin") {
              this.bmlfOpenLookupModal('UserGroupSetupListWithoutTenant', this.msScreenName, 'UserGroupSetupListWithoutTenant', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TITLE');
            } else {
              this.bmlfOpenLookupModal('UserGroupList', this.msScreenName, 'UserGroupSetupList', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TITLE');
            }

          } else if (this.mbIsAddMode) {
            this.mbIsAddMode = false;
            this.bmlfRefreshForm();
          } else if (this.mbIsCopyMode) {
            this.mbIsCopyMode = false;
            this.bmlfCopyData();
          } else if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            if (this.msAdminType == "Super Admin") {
              localStorage.removeItem('TenantIdWiseforGroup');
              localStorage.removeItem('TenantIdSearchLookupforGroup');
              this.router.navigate(["/main/admin/user-group-list"]);
            } else {
              this.router.navigate(["/main/admin/user-group"]);
            }
          } else {
            // this.bmlfGetProductData();
          }
        } else {
          this.mbIsSearchMode = false;
          this.mbIsAddMode = false;
          this.mbIsCopyMode = false;
          this.mbIsDeleteMode = false;
          this.mbIsCloseMode = false;
          this.toastMessageService.notifyError(res.Response[0].ResponseText)
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfCloseUserGroup(): void {
    if (this.mbIsChangeData == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      if (this.msAdminType == "Super Admin") {
        localStorage.removeItem('TenantIdWiseforGroup');
        localStorage.removeItem('TenantIdSearchLookupforGroup');
        this.router.navigate(["/main/admin/user-group-list"]);
      } else {
        this.router.navigate(["/main/admin/user-group"]);
      }
    }
  }
  // So Footer Action finction
  bmlfFooterActionFn(event) {
    if (event == 'searchEvn') {
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsSearchMode = true
        return false;
      }
      if (this.msAdminType == "Super Admin") {
        this.bmlfOpenLookupModal('UserGroupSetupListWithoutTenant', this.msScreenName, 'UserGroupSetupListWithoutTenant', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TITLE');
      } else {
        this.bmlfOpenLookupModal('UserGroupList', this.msScreenName, 'UserGroupSetupList', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TITLE');
      }

    } else if (event == 'addEvn') {
      // if (!this.utilService.mbWrite) {
      //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      //   return false;
      // };
      if (!this.bmlfValidData()) {
        return;
      }
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsAddMode = true
        return false;
      }
      this.bmlfRefreshForm();
    } else if (event == 'deleteEvn') {
      // if (!this.utilService.mbDelete) {
      //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      //   return false;
      // };
      if (this.mobjUserGroupModel.Group_ID == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbIsChangeData) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
        return false;
      }
      if (this.mobjUserGroupModel.Group_ID == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbIsChangeData) {
        this.bmlfRefreshForm();
        return false;
      }
      this.mbShowDeleteDialog = true;
    } else if (event == 'copyEvn') {
      // if (!this.utilService.mbWrite) {
      //   this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      //   return false;
      // };
      if (CustomValidator.bmgfNullCheckValidator(this.mobjUserGroupModel.Group_ID)) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate("COMMON.MSG.COPY"));
        document.getElementById("txtPrdKey").focus();
        return;
      }
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsCopyMode = true;
        return false;
      }
      this.bmlfCopyData();
    }
  }
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsSearchMode) {
        this.mbIsSearchMode = false;
        if (this.msAdminType == "Super Admin") {
          this.bmlfOpenLookupModal('UserGroupSetupListWithoutTenant', this.msScreenName, 'UserGroupSetupListWithoutTenant', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TITLE');
        } else {
          this.bmlfOpenLookupModal('UserGroupList', this.msScreenName, 'UserGroupSetupList', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TITLE');
        }

      } else if (this.mbIsAddMode) {
        this.mbIsAddMode = false;
        this.bmlfRefreshForm();
      } else if (this.mbIsCopyMode) {
        this.mbIsCopyMode = false;
        this.bmlfCopyData();
      } else if (this.mbIsDeleteMode) {
        this.mbIsDeleteMode = false;
        this.mbShowDeleteDialog = true;
      }
      else {
        if (this.msAdminType == "Super Admin") {
          localStorage.removeItem('TenantIdWiseforGroup');
          localStorage.removeItem('TenantIdSearchLookupforGroup');
          this.router.navigate(['/main/admin/user-group-list']);
        } else {
          this.router.navigate(['/main/admin/user-group']);
        }
      }
    } else {
      this.mbSaveDialogView = false;
      this.mbIsSearchMode = false;
      this.mbIsCopyMode = false;
      this.mbIsAddMode = false;
      this.mbIsCloseMode = false;
    }
  }
  // Delete Sales Order Dialog events
  bmlfonDialogAction(psAction) {
    if (psAction == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (psAction == 'Yes') {
      if (this.msFormMode == "I") {
        this.bmlfRefreshForm();
        return false;
      }
      let parrHeaderData = [];
      parrHeaderData = [{ Group_Id: this.mobjUserGroupModel.Group_ID, CompanyID: this.TennatId }]
      let reqData: any = {
        JSON: JSON.stringify({
          ParaData: parrHeaderData,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteUserGroup(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
            this.mbShowDeleteDialog = false;
            this.bmlfRefreshForm();
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }
  bmlfOpenSelectedRecord(data) {
    let param = { grpId: data.Group_ID, grpTyp: data.Group_Type };
    if (this.msAdminType == "Super Admin") {
      this.utilService.bmgfRefreshReuse('/main/admin/user-group-list/manage', true, param);
    } else {
      this.utilService.bmgfRefreshReuse('/main/admin/user-group/manage', true, param);
    }

  }
  // Refresh Sales Order
  bmlfRefreshForm() {
    if (this.msAdminType == "Super Admin") {
      localStorage.setItem("TenantIdWiseforGroup", this.TennatId);
      this.utilService.bmgfRefreshReuse('/main/admin/user-group-list/manage', false);

    } else {
      this.utilService.bmgfRefreshReuse('/main/admin/user-group/manage', false);
    }

  }
  // Copy Current  Data
  bmlfCopyData() {
    let HeaderData = Object.assign({}, this.mobjUserGroupModel);
    HeaderData.Group_ID = "";
    let parrCopyData = {
      HeaderData: HeaderData,
      prdKey: this.mobjUserGroupModel.Group_ID,
      DetailData: this.marrModuleList
    }
    localStorage.setItem('copyUserGroupData', JSON.stringify(parrCopyData));
    localStorage.setItem("TenantIdWiseforGroup", this.TennatId);
    this.bmlfRefreshForm();
  }
  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mobjUserGroupModel.Group_ID;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/User Group Setup.pdf', '_blank');
  }
  bmlfTenant() {
    this.bmlfOpenLookupModal('TenantID', this.msScreenName, 'TenantID', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.TENANTID');
  }

  bmlfAssignedUsers() {
    this.bmlfOpenLookupModal('AssignedUsers', this.msScreenName, 'AssignedUsers', 'click', '', 'custom', 'cmdSearch', 'USERGROUPSETUP.ASSIGNEDUSERS');
  }

}
