import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';


@Injectable({
    providedIn: 'root'
})

export class UserValidationService {
    constructor(
        private httpExt: HttpCleintExtService,
        private appSettings: AppSettingsService
    ) { }
    public bmlfGetUserValidation(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUserValidation`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfSaveUserValidation(headerData: any) {

        const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveUserValidation`;
        return this.httpExt.post<any>(url, headerData, null);

    }

    public bmlfDeleteUserValidation(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteUserValidation`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfDeleteLineUserValidation(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteLineUserValidation`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetJSONFields(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUserValidationJSONLookup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}