import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
})
export class TenantSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    
    public GetTenantSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetTenantSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public CreateTenantSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateTenantSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public UpdateTenantSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateTenantSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public DeleteTenantSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteTenantSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
  

}