  ngOnInit(): void {

    /*--------- get screen permission --------*/
    // this.utilService.getBMEScreePermission(this.msScreenName);
    //this.msAdminType = localStorage.getItem("isLoginType")
    this.msAdminType = "Super Admin";
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.msTenantID = this.route.snapshot.queryParamMap.get('TenantID');
    this.msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
    this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(this.msCopyRecord) ? "N" : this.msCopyRecord;
    this.msTenantID = this.msTenantID == null ? "" : this.msTenantID;

    if (this.msTenantID != "") {
      this.bmlfGetData();
    } else {
      this.msFormMode = "I";
      this.mbChange = false;
      setTimeout(function () { document.getElementById('txtTenantDesc').focus(); }, 10);
    }
    if (this.msCopyRecord && this.msFormMode == "I") {
      this.mbChange = true;
    }
  }
  bmlfGetData() {

    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "TenantID": this.msTenantID
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.GetTenantSetup(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        this.mbChange = false;
        if (res != null && res.HeaderData.length > 0) {
          this.msFormMode = "U";
          this.mobjTenantSetup = res.HeaderData[0];
          //  this.mobjTenantSetup.DefaultOTP=Number(this.mobjTenantSetup.DefaultOTP);
          if (this.mobjTenantSetup.Skip2FASecurity == 1) {
            this.mbIsActive = true;
          } else {
            this.mbIsActive = false;
          }

          if (this.mobjTenantSetup.SkipExpiryDate == 1) {
            this.mbIsSkipDate = true;
          } else {
            this.mbIsSkipDate = false;
          }

          this.mobjTenantSetup.ExpiryDate = new Date(res.HeaderData[0].ExpiryDate)


        } else {
          this.msFormMode = "I";
        }
        if (this.msCopyRecord == "Y" && !this.mbIsSearchMode) {
          this.msFormMode = "I";
          this.mobjTenantSetup.TenantID = "";
          this.mobjTenantSetup.TenantDesc = '';
          this.mbChange = true;
        }
      })
  }

  bmlfonChange() {
    this.mbChange = true;

  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }


  bmlfOpenLookupadminl(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjTenantSetup[psField] = "";
      this.mbIsLoading = false;
      return false
    }
    this.userFieldService.setConnectionAdmin().subscribe(res => {
      if (res.token) {
        let psReqData = { Token: res.token }
        this.userFieldService.bmgfGetAdminReminderCode(psReqData).subscribe(res => {

          this.mbIsLoading = false;
          if (res == null) {
            this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
            return false;
          }
          if (Object.entries(res).length > 0 && res.LookupData[0].length > 0) {
            res.LookupData.forEach((ele) => {
              Object.keys(ele).forEach((key) => {
                let psKey = key.toUpperCase();
                if (psKey.search('DATE') != -1) {
                  if (ele[key] && ele[key] != '') {
                    ele[key] = moment(ele[key]).format(this.msCustomDateFormat != undefined && this.msCustomDateFormat.toUpperCase());
                  } else {
                    ele[key] = moment().format(this.msCustomDateFormat != undefined && this.msCustomDateFormat.toUpperCase());
                  }
                }
              });
            })

            this.marrLookupGridData = res.LookupData[0];
            this.mnumLineItemRowIndex = pnumRowIdx;
            this.mobjLookupKeyData.field = psField;
            this.mobjLookupKeyData.gridData = this.marrLookupGridData;
            this.mobjLookupKeyData.type = psType;
            this.mobjLookupKeyData.eleId = psEleId;

            if (psValue) {
              this.mobjLookupKeyData.searchValue = psValue.toString();
            } else {
              this.mobjLookupKeyData.searchValue = '';
            }
            if (psEvent == 'blur' && psValue) {
              let parrFilterdArr: any = [];
              /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
              let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
              parrFilterdArr = this.marrLookupGridData.filter(obj => {
                if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
                  return obj;
                }
              });
              if (parrFilterdArr.length == 1) {
                this.mobjLookupKeyData.value = parrFilterdArr[0];
                this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
                this.bmlfSaveLookupField(this.mobjLookupKeyData);
                this.mbIsLookupModal = false;
              } else {
                this.mbIsLookupModal = true;
              }
            } else if (psEvent == 'click') {
              this.mbIsLookupModal = true;
            }

          } else {
            this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          }
        }, err => {
          console.log(err);
          this.mbIsLoading = false;

        })
      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.SERVERERROR');
        return false;
      }
    })
  }



