  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {
    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    if (pobjEvent.type == 'custom') {
      if (pobjEvent.field == "TenantSetupList") {
        this.msTenantID = pobjEvent.valueKey;
        this.bmlfGetData()
        this.mbChange = true;
      } else {
        this.mobjTenantSetup.ReminderCode = pobjEvent.value.Code; // code
        this.mobjTenantSetup.TenantDesc = pobjEvent.value.Name; // name
        this.mobjTenantSetup.ExpiryDate = new Date(pobjEvent.value.PaymentExpiryDate); // Expired Date
        this.mobjTenantSetup.WMSUsers = pobjEvent.value.AddonUser; // WMS User
        this.mobjTenantSetup.BMEUsers = pobjEvent.value.WebUser; // BME User
        this.mbChange = true;
      }
    }
  }



  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if (psField == "ReminderCode") {
      return 'Code';
    }
  }

  bmlfCloseLookupModal(pobjEvent): void {
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    if (pobjEvent.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjEvent.field);
      let parrFilterData: any = [];
      if (psInputKey  && pobjEvent.searchValue != null) {
        parrFilterData = pobjEvent.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjEvent.searchValue && pobjEvent.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0 || pobjEvent.searchValue == null) {
        if (pobjEvent.field == 'ReminderCode') {
          this.mobjTenantSetup[pobjEvent.field] = '';
        }
        document.getElementById(pobjEvent.eleId).focus();
      }
    }
  }




