    // else if (this.mobjTenantSetup.Skip2FASecurity == 1 && (CustomValidator.bmgfNullCheckValidator(this.mobjTenantSetup.DefaultOTP) || (this.mobjTenantSetup.DefaultOTP).toString() == "0000")) {
    //   this.bmlfOpenErrorBlock("TENANTSETUP.DEFAULTOTPEMPTY");
    //   return false;
    // } 
    // else if(this.mobjTenantSetup.Skip2FASecurity == 1 && this.mobjTenantSetup.DefaultOTP != null && (this.mobjTenantSetup.DefaultOTP).toString().length>=0){
    //   if(((this.mobjTenantSetup.DefaultOTP).toString().length)<4) {
