import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
})
export class SequenceNumberSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }

    public bmlfGetSequenceNumberSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetSequenceNumberSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveSequenceNumberSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSequenceNumberSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    
   
}