import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
})
export class DefaultSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }

    public bmlfGetDefaultSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetDefaultSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveDefaultSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveDefaultSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    
   
}