import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
})
export class CustomFieldConfigurationService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }

    public bmlfGetCustomizationTool(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetCustomizationTool`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveCustomizationTool(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveCustomizationTool`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfDeleteCustomizationTool(headerData: any, type) {
        if (type == true) {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteCustomizationTool`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteLineCustomizationTool`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

}