import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import * as moment from 'moment';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { WindowsServiceConfigurationService } from '../../http/windows-service-configuration.service';
import { WindowsServiceConfigurationModal, WindowsServiceConfigurationModalHeader } from '../../models/windows-service-configuration.modal';
import { WindowsServiceConfiguration } from '../../constant/windos-service-configuration.const';
import { cloneDeep } from 'lodash'
@Component({
  selector: 'app-windows-service-configuration',
  templateUrl: './windows-service-configuration.component.html',
  styleUrls: ['./windows-service-configuration.component.scss']
})
export class WindowsServiceConfigurationComponent implements OnInit {
  public marrOccursEvery = [{ id: 1, value: '1 Minute' }, { id: 2, value: '5 Minutes' }, { id: 3, value: '1 Hour' }, { id: 4, value: '4 Hours' }, { id: 5, value: 'Day' }, { id: 6, value: 'Week' }, { id: 7, value: 'Month' }, { id: 8, value: 'Quarter' }, { id: 9, value: 'Year' }]
  public marrLineItemCol: any = WindowsServiceConfiguration.garrLineGridCol.map(ele => Object.assign({}, ele));
  public marrGridData: any = [];
  public msDateTimeformat: string = "";
  public mbIsLoading: boolean = false;
  public msPrdKey: string;
  public msScreenName: string = 'Windows Service Configuration';
  public mbIsChangeData: boolean = false;
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public autoCorrect: boolean = true;
  public msFormMode: string = "I";
  /**---Object Initialize---- */
  public mObjModel: WindowsServiceConfigurationModalHeader = new WindowsServiceConfigurationModalHeader();
  public mobjWindowsServiceInfoModel: WindowsServiceConfigurationModal = new WindowsServiceConfigurationModal();
  public mobjUserData: any = {};
  public mnumDeleteLineIndex: number;
  public mbDeleteLineItemDialog: boolean = false;
  public mbShowDeleteDialog: boolean = false;
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModalTitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  /**---END : Lookup realted field--- */

  public msCloseActionModalName: string = "";
  public mbSaveDialogView: boolean = false;
  // public mbIsSearchMode: boolean = false;
  // public mbIsAddMode: boolean = false;
  // public mbIsCopyMode: boolean = false;
  // public mbIsDeleteMode: boolean = false;
  public mbIsCloseMode: boolean = false;
  public msCopyRecord: string = "N";

  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/


  public isDislabeFinanceSection: boolean = false;
  public mskeyValue: string = ""; //FieldExtender
  public CompanyInfoList: any = [];
  public CustomFinanceList: any = [];
  public isCheckAllreadyExcited: boolean = false;
  public mbShowIsCompanyDatabaseAldy: boolean = false;
  public mbShowDeleteDialogCheck: boolean = false;
  public mbShowOnlyAlphabet: boolean = false;
  public mbShowifCompanyIDExcited: boolean = false;
  public mbShowifAlreadyAttached: boolean = false;
  public mbdislablebutton: boolean = false;
  public mbDescriptionDialog = false;
  public msparameterText = '';
  public marrDuplicateArray = [];

  constructor(private route: ActivatedRoute,
    private router: Router,
    private formService: WindowsServiceConfigurationService,
    private lookupService: LookupService,
    private toastMessageService: ToastMessageService,
    private dateFormatService: DateFormatService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService,
    private lsService: LocalStorageService) {
  }
  ngOnInit() {
    /**--- Get default date format from service--- */
    this.msDateTimeformat = this.dateFormatService.getDateTimeFormat();
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
  }


  bmlfGetWindowsServiceConf() {
    let parrHeaderData = [{
      CompanyID: this.mObjModel.CompanyID
    }]
    let pobjJData = {
      HeaderData: parrHeaderData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfGetWindowServiceConfig(pobjJson).subscribe(
      res => {
        this.mbIsChangeData = false;
        this.mbIsLoading = false;
        if (res != null && res.HeaderData.length > 0) {
          this.mbIsChangeData = false;
          this.marrGridData = res.HeaderData;
          this.marrDuplicateArray = cloneDeep(this.marrGridData);
          if (this.marrGridData.length > 0) {
            this.marrGridData.forEach(ele => {
              //ele.Flag='M';

              if (!CustomValidator.bmgfNullCheckValidator(ele.LastRunTime)) {

                // if(ele.LastRunTime != '1900-01-01T00:00:00'){
                //   ele.LastRunTime=moment(ele.LastRunTime).format('DD/MM/YY, HH:mm:ss');
                // }
                if ((ele.LastRunTime != null || ele.LastRunTime != '') && ele.LastRunTime != '1900-01-01T00:00:00') {
                  ele.LastRunTime = moment(ele.LastRunTime).format('DD/MM/YY, HH:mm:ss');
                }
                else {
                  ele.LastRunTime = '';
                }
              }
              if (ele.ScheduleTime) {
                //    console.log("ele.ScheduleTime", ele.ScheduleTime)
                ele.ScheduleTime = new Date(ele.ScheduleTime);
              }
            })
          }
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }
  bmlfOnFormFieldChange(rowIndex) {
    this.mbIsChangeData = true;
    //  this.marrGridData[rowIndex].Flag='M'
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }


  /**--------Start: Lookup Functionality----- */
  // LOOKUP MODAL 
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mnumLineItemRowIndex = pnumRowIdx;
    this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjWindowsServiceInfoModel[psField] = "";
      if (psField == 'CompanyID') {
        this.mObjModel.CompanyIDDesc = "";
        this.marrGridData = [];
      }
      if (psField == 'Recuserid') {
        if (this.mnumLineItemRowIndex != null) {
          this.mbIsChangeData = true;
          this.marrGridData[this.mnumLineItemRowIndex][psField] = '';
        }
      }


      this.mbIsLoading = false;
      return false
    }
    let psReqData: any = {}
    if (psField == 'Recuserid') {
      psReqData = { ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: this.mObjModel.CompanyID };
      this.lookupService.getLookup(psReqData).subscribe(res => {
        this.mbIsLoading = false;
        if (res == null) {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          return false;
        }
        if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
          res.LookupData.forEach((ele) => {
            Object.keys(ele).forEach((key) => {
              let psKey = key.toUpperCase();
              if (psKey.search('DATE') != -1) {
                if (ele[key] && ele[key] != '') {
                  ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
                } else {
                  ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
                }
              }
            });
          })
          this.marrLookupGridData = res.LookupData;
          this.mobjLookupKeyData.field = psField;
          this.mobjLookupKeyData.gridData = this.marrLookupGridData;
          this.mobjLookupKeyData.type = psType;
          this.mobjLookupKeyData.eleId = psEleId;

          if (psValue) {
            this.mobjLookupKeyData.searchValue = psValue.toString();
          } else {
            this.mobjLookupKeyData.searchValue = '';
          }
          if (psEvent == 'blur' && psValue) {
            let parrFilterdArr: any = [];
            /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
            let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
            parrFilterdArr = this.marrLookupGridData.filter(obj => {
              if (obj[pobjFirstInput].toString().toUpperCase() == psValue.toUpperCase()) {
                return obj;
              }
            });
            if (parrFilterdArr.length == 1) {
              this.mobjLookupKeyData.value = parrFilterdArr[0];
              this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
              this.bmlfSaveLookupField(this.mobjLookupKeyData);
              this.mbIsLookupModal = false;
            } else {
              this.mbIsLookupModal = true;
            }
          } else if (psEvent == 'click') {
            this.mbIsLookupModal = true;
          }

        } else {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        }
      }, err => {
        console.log(err);
        this.mbIsLoading = false;
      })
    }
    else {
      psReqData = { ScreenName: psScreenName, LookUpKey: psLookUpKey }
      this.userFieldService.bmlfAdminLookupData(psReqData).subscribe(res => {
        this.mbIsLoading = false;
        if (res == null) {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          return false;
        }
        if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
          res.LookupData.forEach((ele) => {
            Object.keys(ele).forEach((key) => {
              let psKey = key.toUpperCase();
              if (psKey.search('DATE') != -1) {
                if (ele[key] && ele[key] != '') {
                  ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
                } else {
                  ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
                }
              }
            });
          })
          this.marrLookupGridData = res.LookupData;
          this.mobjLookupKeyData.field = psField;
          this.mobjLookupKeyData.gridData = this.marrLookupGridData;
          this.mobjLookupKeyData.type = psType;
          this.mobjLookupKeyData.eleId = psEleId;

          if (psValue) {
            this.mobjLookupKeyData.searchValue = psValue.toString();
          } else {
            this.mobjLookupKeyData.searchValue = '';
          }
          if (psEvent == 'blur' && psValue) {
            let parrFilterdArr: any = [];
            /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
            let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
            parrFilterdArr = this.marrLookupGridData.filter(obj => {
              if (obj[pobjFirstInput].toString().toUpperCase() == psValue.toUpperCase()) {
                return obj;
              }
            });
            if (parrFilterdArr.length == 1) {
              this.mobjLookupKeyData.value = parrFilterdArr[0];
              this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
              this.bmlfSaveLookupField(this.mobjLookupKeyData);
              this.mbIsLookupModal = false;
            } else {
              this.mbIsLookupModal = true;
            }
          } else if (psEvent == 'click') {
            this.mbIsLookupModal = true;
          }

        } else {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        }
      }, err => {
        console.log(err);
        this.mbIsLoading = false;
      })
    };


  }

  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {
    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    this.mbIsChangeData = true;
    let psInputColumn = this.bmlfGetInputColumn(pobjEvent.field);
    let psInputColumnDesc = this.bmlfGetInputColumnDesc(pobjEvent.field)
    if (pobjEvent.type == 'custom') {
      if (pobjEvent.field == 'Recuserid') {
        if (this.mnumLineItemRowIndex != null) {
          this.marrGridData[this.mnumLineItemRowIndex]['Recuserid'] = pobjEvent.value['User_ID']
        }
      }
      else {
        this.mObjModel[pobjEvent.field] = pobjEvent.value[psInputColumn];
        this.mObjModel[pobjEvent.field + 'Desc'] = pobjEvent.value[psInputColumnDesc];
        this.bmlfGetWindowsServiceConf();
      }

    }
  }


  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if (psField == 'CompanyID') {
      return 'Mfg_Company_ID';
    }
  }

  // get Lookup Value Column Desc
  bmlfGetInputColumnDesc(psField) {
    if (psField == 'CompanyID') {
      return 'Mfg_Company_Name';
    }
  }


  //Close LOOKUP MODAL
  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        this.mobjWindowsServiceInfoModel[pobjData.field] = '';
        // this.mbIsChangeData = true;
      }
      document.getElementById(pobjData.eleId).focus();
    }
  }



  /**--------End: Lookup Functionality----- */



  bmlfSubmitData(pbClose: boolean = false): void {
    if (this.mbIsChangeData == false) {
      return;
    }
    this.bmlfSaveWindowsServiceConf();
  }

  bmlfSaveWindowsServiceConf(): void {
    //  this.mbIsLoading = true;
    let parrHeaderData = [];
    this.marrGridData.forEach((ele, i) => {
      if (ele.Active && ele.OccursEvery == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('WINDOWSSERVICECONFIGURATION.MSG.PLEASEADDOCCURSEVERY') + (i + 1));
        return;
      }
    })

    this.marrGridData.forEach(ele => {
      // console.log("element", ele);
      let pObjData = Object.assign({}, this.mobjWindowsServiceInfoModel);
      pObjData.CompanyID = this.mObjModel.CompanyID,
        pObjData.Active = ele.Active;
      pObjData.Flag = ele.Flag;
      pObjData.ScheduleTime = (ele.ScheduleTime != null && ele.ScheduleTime != '') ? moment(ele.ScheduleTime).format(CommonConstants.gsServerDateTimeFormat) : '';
      pObjData.LastRunTime = (ele.LastRunTime != null && ele.LastRunTime != '') ? moment(new Date(ele.LastRunTime)).format(CommonConstants.gsServerDateFormat) : '';
      pObjData.ServiceName = ele.ServiceName;
      pObjData.ExeName = ele.ExeName;
      pObjData.OccursEvery = ele.OccursEvery;
      pObjData.ConnectionString = ele.ConnectionString;
      pObjData.OnNDay = Number(ele.OnNDay);
      pObjData.JSONParameters = ele.JSONParameters;
      pObjData.Recuserid = ele.Recuserid ? ele.Recuserid : '';
      parrHeaderData.push(pObjData);
    })
    let tempHeaderData = cloneDeep(parrHeaderData);
    let tempArray = [];
    this.marrDuplicateArray.forEach((ele, i) => {
      tempArray.push(ele.LastRunTime);
    })
    tempHeaderData.forEach((ele, i) => {
      ele.LastRunTime = (tempArray[i] != null && tempArray[i] != '') ? moment(new Date(tempArray[i])).format(CommonConstants.gsServerDateTimeFormat) : '';
    })
    tempHeaderData;


    //  parrHeaderData.push(Object.assign({}, this.mobjCompanyInfoModel));
    let pobjJData = {
      HeaderData: tempHeaderData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveWindowServiceConfig(pobjJson).subscribe(
      res => {
        this.mbIsChangeData = false;
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mbIsChangeData = false;
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate(res.Response[0].ResponseText));
          this.bmlfGetWindowsServiceConf();

          if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.router.navigate(["/main/admin/windows-service-configuration"]);
          }
        } else {
          this.mbIsChangeData = false;
          this.mbIsCloseMode = false;
          this.toastMessageService.notifyError(res.Response[0].ResponseText)
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }


  bmlfCloseCompanyInfo(): void {
    if (this.mbIsChangeData == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      this.router.navigate(["/main"]);
    }
  }







  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      this.router.navigate(['/main']);

    } else {
      this.mbSaveDialogView = false;
      this.mbIsCloseMode = false;
    }
  }



  bmlfOpenSelectedRecord(data) {
    let param = { prdKey: data.Mfg_Company_ID };
    this.utilService.bmgfRefreshReuse('/main/admin/windows-service-configuration', true, param);
  }

  // Refresh Sales Order
  bmlfRefreshForm() {
    this.utilService.bmgfRefreshReuse('/main/admin/windows-service-configuration', false);
  }



  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mObjModel.CompanyID;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }


  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Windows Service Configuration.pdf', '_blank');
  }

  bmlfonSelectRow(rowNum) {
    this.mbIsChangeData = true;
  }

  bmlfDescriptionModal(event, rowIndex, value) {
    this.mbIsChangeData = true;
    this.mnumLineItemRowIndex = rowIndex;
    if (event == 'open') {
      this.mbDescriptionDialog = true;
      this.msparameterText = value;
    } else if (event == 'close') {
      this.mbDescriptionDialog = false;
      this.marrGridData[this.mnumLineItemRowIndex]['JSONParameters'] = value;
    }
  }





}
